import work from 'webworkify-webpack';
import { abrGetUrl } from '../abr/abr-get-url';
import { AbrManifest } from '../abr/abr-manifest';
import Multirate, { MultirateItem } from '../abr/multirate';
import { FlvTag } from '../demux/flv/flv';
import FlvDemuxerInline from '../demux/flv/flv-demuxer-inline';
import FlvTagDump from '../demux/flv/flv-tag-dump';
import Loader from '../io/loader';
import { FLVTransConfig } from '../types/core';
import { ILoaderCallback, ILoaderConfig, ILoaderContext, ILoaderStats } from '../types/io';
import { Log } from '../utils/log';
import { ErrorDetails, ErrorTypes } from './errors';
import LasEvents from './events';
import Media from './media';
import Observer from './observer';
import { REPORT_TYPES } from './report-types';

/**
 * TransFLV
 * @class TransFLV
 * @param {object} config 
 */
const tag = 'TransFLV';
export default class TransFLV extends Observer {
    private _config: FLVTransConfig;
    private _media: Media;
    private _w?: Worker;
    private _flv?: FlvDemuxerInline;
    private _observer: Observer;

    private _loader: Loader<ILoaderContext> | null = null;
    private _loaderConf: ILoaderConfig;
    private _loaderCallbacks: ILoaderCallback<ILoaderContext>;
    private _multirate?: Multirate;
    private _contiguous: boolean;
    private _remuxId: number;
    private _discontinuity: boolean = false;
    private _accurateTimeOffset: boolean = false;
    private _baseTimeSec: number = 0;
    private _tagDump: FlvTagDump;
    private _currentUrl?: any;
    private _lastDTS: number = 0;
    private _isAbr: boolean = false;
    private _progressTime: number = 0;

    constructor(config: FLVTransConfig, media: Media) {
        super();
        this._config = config;
        this._media = media;

        if (config.manifest) {
            if (AbrManifest.verify(config.manifest)) {
                this._isAbr = true;
            } else {
                config.hasVideo = config.manifest.hasVideo;
                config.hasAudio = config.manifest.hasAudio;
            }
        }

        this._loaderConf = {
            connectionTimeout: this._config.connectionTimeout,
            transmissionTimeout: this._config.transmissionTimeout,
            maxRetry: 0,
            retryDelay: 0,
            useFetch: true
        };
        this._loaderCallbacks = {
            onProgress: this._onProgress.bind(this),
            onError: this._onLoaderError.bind(this),
            onEnd: this._onLoaderEnd.bind(this),
            onAbort: this._onAbort.bind(this)
        };
        this._contiguous = false;
        this._remuxId = 1;

        const observer = (this._observer = new Observer());
        const onMessage = this._onMessage;
        observer.on(LasEvents.PARSING_INIT_SEGMENT, onMessage);
        observer.on(LasEvents.PARSING_DATA, onMessage);
        observer.on(LasEvents.ERROR, onMessage);
        observer.on(LasEvents.SCRIPT_PARSED, onMessage);
        observer.on(LasEvents.DISCONTINUITY, onMessage);
        observer.on(LasEvents.FLV_HEAD, onMessage);

        this._tagDump = new FlvTagDump(observer, this._onKeyframe);

        if (this._config.webWorker) {
            Log.i(tag, 'webWorker');
            this._onWorkMessage = this._onWorkMessage.bind(this);
            this._w = work(require.resolve('../demux/flv/flv-demuxer-worker'));
            if (this._w) {
                this._w.addEventListener('message', this._onWorkMessage);
                this._w.postMessage({
                    cmd: 'init',
                    config: this._config,
                    data: { remuxId: this._remuxId },
                    vendor: navigator.vendor
                });
                return;
            }
        }
        this._flv = new FlvDemuxerInline(observer, this._config, { remuxId: this._remuxId }, navigator.vendor);
        this._flv.init();
    }

    public init(): void {
        if (this._isAbr && !this._multirate) {
            this._multirate = new Multirate(this._config, this._media);
            this._multirate.on(LasEvents.MANIFEST_PARSED, this._onMessage);
            this._multirate.init();
        }
    }

    private _onWorkMessage(ev: any) {
        const data = ev.data;
        if (ev.data.event === LasEvents.PARSING_DATA) {
            data.data.payload = data.payload;
        }
        this._onMessage(ev.data.event, data.data);
    }

    private _onKeyframe = (timestamp: number): MultirateItem | undefined => {
        if (this._multirate) {
            let result = this._multirate.onKeyFrame(timestamp);
            if (result) {
                if (this._tagDump) {
                    this._tagDump.reset();
                }
                // 
                let info = Object.assign({}, result);
                this._baseTimeSec = this._lastDTS;
                this.emit(LasEvents.LEVEL_SWITCHING, { level: info.level, startSec: this._baseTimeSec, smooth: true });
                this._media.updateStreamTime(0, 0);
                return info;
            }
        }
        return;
    }

    /**
     * instance ioloader
     */
    public loadSource(): void {
        let mr = this._multirate;
        if (mr) {
            let data = mr.levels[mr.currentLevel];
            if (data) {
                this._load(abrGetUrl(data.url, this._config.defaultSpts), mr.currentLevel);
            } else {
                this.emit(LasEvents.ERROR, {
                    type: ErrorTypes.OTHER_ERROR,
                    details: ErrorDetails.PARSING_ERROR,
                    fatal: true,
                    reason: 'manifest parse error'
                });
            }
        } else {
            this._load(this._config.src);
        }
    }

    /**
     * destroy
     */
    public destroy(): void {
        if (this._loader) {
            this._loader.destroy();
            this._loader = null;
        }
        if (this._w) {
            this._w.postMessage({ cmd: 'destroy' });
            this._w.removeEventListener('message', this._onWorkMessage);
            this._w.terminate();
        }
        if (this._flv) {
            this._flv.destroy();
            this._flv = undefined;
        }

        if (this._multirate) {
            this._multirate.removeAllListeners();
            this._multirate.destory();
        }

        const observer = this._observer;
        if (observer) {
            observer.removeAllListeners();
        }
    }

    public get autoLevelEnabled(): boolean {
        if (this._multirate) {
            return this._multirate.autoLevelEnabled;
        }
        return false;
    }

    public get levels() {
        if (this._multirate) {
            return this._multirate.levels;
        }
        return [];
    }

    public get nextLevel(): number {
        if (this._multirate) {
            return this._multirate.nextLevel;
        }
        return 0;
    }

    public set nextLevel(value: number) {
        const mr = this._multirate;
        if (mr) {
            mr.nextLevel = value;
        }
    }

    public get currentLevel(): number {
        if (this._multirate) {
            return this._multirate.currentLevel;
        }
        return 0;
    }

    public set currentLevel(value: number) {
        const mr = this._multirate;
        if (mr) {
            let load = value >= 0 || value !== mr.currentLevel;
            mr.currentLevel = value;
            const data = mr.levels[mr.currentLevel];
            if (load && data) {
                this._currentUrl = abrGetUrl(data.url, this._config.defaultSpts);
                this._refreshRemuxId();
                this._contiguous = false;
                this._discontinuity = true;
                this._accurateTimeOffset = false;
                if (this._tagDump) {
                    this._tagDump.reset();
                }
                this._baseTimeSec = this._media.currentTime;
                this.emit(LasEvents.LEVEL_SWITCHING, { level: mr.currentLevel, startSec: this._baseTimeSec, smooth: false });
                this._load(this._currentUrl, mr.currentLevel);
            }
        }
    }


    private _onMessage = (ev: string, data: any): void => {
        switch (ev) {
            case LasEvents.FLV_HEAD:
                if (this._w) {
                    this._w.postMessage({ cmd: 'flvHead', hasAudio: data.hasAudio, hasVideo: data.hasVideo });
                } else if (this._flv) {
                    this._flv.flvHead(data.hasAudio, data.hasVideo);
                }
                break;
            case LasEvents.PARSING_INIT_SEGMENT:
                this.emit(LasEvents.PARSING_INIT_SEGMENT, data);
                break;
            case LasEvents.PARSING_DATA:
                {
                    if (data.extra && data.extra.remuxId !== this._remuxId) {
                        // 
                        break;
                    }
                    if (data.type === 'audio' && data.startDTS > this._baseTimeSec) {
                        this._media.updateStreamTime(data.streamDTS, data.startDTS);
                    }
                    this._lastDTS = data.startDTS;
                    this.emit(LasEvents.PARSING_DATA, {
                        data: data.payload,
                        type: data.type,
                        startDTS: data.startDTS || 0,
                        endDTS: data.endDTS || 0,
                        startPTS: data.startPTS || 0,
                        endPTS: data.endPTS || 0,
                        duration: data.endDTS - data.startDTS,
                        framesInfo: data.framesInfo
                    });
                }
                break;
            case LasEvents.DISCONTINUITY:
                if (this._w) {
                    this._w.postMessage({ cmd: 'flush' });
                } else if (this._flv) {
                    this._flv.flush();
                }
                this._tagDump.reset();
                this._discontinuity = true;
                this._accurateTimeOffset = false;
                this._contiguous = false;
                this._baseTimeSec = data;
                break;
            default:
                // LOST_FRAMES SCRIPT_PARSED REPORT ERROR END
                this.emit(ev, data);
                break;
        }
    }

    private _load(url: string, index: number = 0): void {
        if (this._loader) {
            this._loader.destroy();
            this._loader = null;
        }
        if (this._multirate) {
            this._multirate.onLevelLoad(index);
        }
        this._currentUrl = url;
        let level = this.levels[index]
        this.emit(LasEvents.REPORT, {
            type: REPORT_TYPES.START_LOAD_STREAM,
            url,
            sync: this._baseTimeSec,
            index: index,
            bitrate: level ? level.bitrate : 0
        });
        if (!this._loader) {
            this._loader = new Loader();
        }
        const context = {
            url,
            progress: true,
            responseType: 'arraybuffer',
            credentials: this._config.credentials
        };
        if (this._loader instanceof Loader) {
            this._loader.load(context, this._loaderCallbacks, this._loaderConf);
        }
    }

    private _append(
        tags: FlvTag[],
        timeOffset: number,
        discontinuity: boolean,
        contiguous: boolean,
        accurateTimeOffset: boolean
    ): void {
        if (this._w) {
            this._w.postMessage({
                cmd: 'append',
                tags,
                timeOffset: timeOffset || 0,
                discontinuity,
                contiguous,
                accurateTimeOffset
            });
        } else if (this._flv) {
            this._flv.append(tags, timeOffset || 0, discontinuity, contiguous, accurateTimeOffset);
        }
    }

    private _onProgress(context: ILoaderContext, data: string | ArrayBuffer, stats: ILoaderStats): void {
        if (!(data instanceof ArrayBuffer)) {
            return;
        }
        if (this._multirate) {
            this._multirate.onLoaderChunk(data.byteLength);
        }
        this.emit(LasEvents.REPORT, {
            type: REPORT_TYPES.LOADER_CHUNK_ARRIVAL,
            byteLength: data.byteLength,
            timeCost: performance.now() - this._progressTime || stats.trequest,
            header: context.responseHeader
        });
        this._progressTime = performance.now();
        const result = this._tagDump.append(data);
        this._append(result.list, this._baseTimeSec, this._discontinuity, this._contiguous, this._accurateTimeOffset);
        this._accurateTimeOffset = true;
        this._contiguous = true;
        this._discontinuity = false;

        // 
        if (result.abr) {
            this._load(result.abr.url, result.abr.level);
        }
    }

    private _onAbort(): void { }

    private _onLoaderError(context: ILoaderContext, status: ILoaderStats): void {
        if (!status.fatal) {
            return;
        }
        const errInfo = {
            type: ErrorTypes.NETWORK_ERROR,
            details: ErrorDetails.LOAD_ERROR,
            fatal: true,
            reason: status.text,
            statusCode: status.code,
            url: context.url
        };
        this.emit(LasEvents.ERROR, errInfo);
    }

    private _onLoaderEnd(): void {
        if (this._w) {
            this._w.postMessage({ cmd: 'end' });
        } else if (this._flv) {
            this._flv.end();
        }
    }

    private _refreshRemuxId(): void {
        this._remuxId++;
        const data = { remuxId: this._remuxId };
        if (this._w) {
            this._w.postMessage({ cmd: 'setExtra', data: data });
        } else if (this._flv) {
            this._flv.setExtra(data);
        }
    }
}
