import Url from 'url-parse';

/**
 * flv
 * @param url 
 * @param spts 0pts0
 */
export function abrGetUrl(url: string, spts?: number): string {
    if (typeof spts === 'undefined') {
        return url;
    }
    const urlparse = new Url(url, true);
    urlparse.query.lasSpts = `${spts}`;
    return urlparse.toString();
}