import Observer from '../core/observer';
import { ATrack } from '../types/remux';
import { ASCInfo, getAudioConfig } from './get-audio-config';

export function parseData(data: Uint8Array, offset: number): ASCInfo {
    return {
        objectType: data[offset + 2] >>> 3, // 5 bits
        sampleingIndex: (data[offset + 2] & 0x07) << 1 | data[offset + 3] >>> 7, // 4 bits
        chanelConfig: (data[offset + 3] & 0x78) >>> 3 // 4 bits
    };
}

export function initTrackConfig(track: ATrack, observer: Observer, data: Uint8Array, offset: number, audioCodec: string = '') {
    if (!track.samplerate) {
        const config = getAudioConfig(observer, parseData(data, offset), audioCodec);
        if (config) {
            track.config = config.config;
            track.timescale = track.samplerate = config.samplerate;
            track.channelCount = config.channelCount;
            track.codec = config.codec;
            track.manifestCodec = config.manifestCodec;
            track.isAAC = true;
        }
    }
}

export function getFrameDuration(samplerate: number): number {
    return 1024 * 1000 / samplerate;
}
