import { EventEmitter } from 'events';
import AbrLevel from './abr/abr-level';
import { ConfigHelper } from './config';
import { ErrorDetails, ErrorTypes } from './core/errors';
import LasEvents from './core/events';
import Media from './core/media';
import MSEController from './core/mse-controller';
import TransFlv from './core/trans-flv';
import { LasConfig } from './types/core';
import Browser from './utils/browser';
import { getErrorCode } from './utils/get-error-code';
import { isSupported } from './utils/is-supported';
import { Log } from './utils/log';
import Monitor from './monitor/monitor';

// 
const MAIN_TIMER_INTERVAL = 200;

const BUFFER_THRESHOLD_DEFAULT = 0.5;
const BUFFER_THRESHOLD_STEP = 1;
const BUFFER_THRESHOLD_MAX = 3.5;

enum STAT {
    NONE,
    WAITING,
    SEEK,
    SELECT_BITRATE,
    INIT
}
/**
 * Las controller
 * @export
 * @class Las
 */
export default class Las extends EventEmitter {
    private tag: string = 'las';
    private _config: LasConfig;
    private _video?: HTMLVideoElement;
    private _mse!: MSEController;
    private _trans?: TransFlv;

    private _stat: STAT = STAT.INIT;
    private _seekOnCanplay: boolean = false;
    private _audioCodecSwap: boolean = false;
    private _error: any;
    private _audioCodec: string = '';
    private _recoverMediaErrorTime: number = 0;
    private _mainTimer: any;
    private _media: Media;

    private _nextLevel: any[] = [];
    private _mediaInfo: any;
    // ()buffer
    private _bufferThreshold: number = BUFFER_THRESHOLD_DEFAULT;
    private _loadStopped: boolean = false;
    private _seekOnUpdateEnd: boolean = false;
    private _playingLevel?: number;
    private _startLevel?: number;
    private _monitor!: Monitor;

    static isSupport(): boolean {
        return isSupported();
    }

    static get version() {
        return __VERSION__;
    }


    static get Events() {
        return LasEvents;
    }

    static get ErrorTypes() {
        return ErrorTypes;
    }

    static get ErrorDetails() {
        return ErrorDetails;
    }

    constructor(config?: LasConfig) {
        super();
        if (!this.off) {
            this.off = this.removeListener;
        }
        this._config = ConfigHelper.processConfig(config);
        this._media = new Media();
        if (!this._config) {
            setTimeout(() => {
                this._onError({
                    type: ErrorTypes.OTHER_ERROR,
                    details: ErrorDetails.CONFIG_ERROR,
                    fatal: true,
                    reason: 'config data error'
                });
            }, 0);
            return;
        }
        if (!Las.isSupport()) {
            setTimeout(() => {
                this._onError({
                    type: ErrorTypes.OTHER_ERROR,
                    details: ErrorDetails.UNSUPPORTED,
                    fatal: true,
                    reason: 'unsupported'
                });
            }, 0);
            return;
        }
        this._mainTimer = null;
        this._stat = STAT.INIT;
        this._startMainTimer();
        this._initMonitor();
        Log.i(this.tag, Las.version, this._config);
    }

    /**
     * videoMSE
     */
    public attachMedia(video: HTMLVideoElement): void {
        this._video = video;
        this._media.attachVideo(this._video);
        this._initMSE(video);
        this._bindVideoEvents();
    }

    /**
     * load
     * @param {string} src src
     */
    public load(src: any = undefined): void {
        this._playingLevel = undefined;
        this._monitor.reset();
        if (src) {
            ConfigHelper.setSrc(this._config, src);
        }
        if (!this._config.src && !this._config.manifest) {
            this._onError({
                type: ErrorTypes.OTHER_ERROR,
                details: ErrorDetails.CONFIG_ERROR,
                fatal: true,
                reason: 'url empty'
            });
            return;
        }
        this._load();
    }

    /**
     * 
     */
    public resume() {
        Log.i(this.tag, 'call resume');
        if (this._loadStopped) {
            this._loadStopped = false;
            this._load();
        }
        if (this._video && this._video.paused) {
            this._video.play();
        }
    }

    /**
     * destroy
     */
    public destroy(): void {
        this._stopMonitor();
        this._stopMainTimer();
        this._unbindVideoEvents();
        this._stopVideo();
    }

    public refresh(): void {
        Log.i(this.tag, 'call refresh');
        if (this._trans && this._mse && this._video) {
            this._startLevel = this._trans.currentLevel;

            this._nextLevel = [];

            this._stopVideo();
            this._initMSE(this._video);

            this._initTrans();
            if (this._trans) {
                this._trans.loadSource();
            }

        } else {
            Log.v(this.tag, 'transmuxer & mediaSource not ready');
        }
    }

    /**
     * 
     */
    public stopLoad(): void {
        Log.i(this.tag, 'call stopLoad');
        if (this._trans) {
            this._destroyTrans();
            this._mse.endOfData();
            this._loadStopped = true;
            this._monitor.onStopLoad();
        }
    }

    public getMediaInfo(): any {
        return Object.assign({}, this._mediaInfo);
    }

    public get autoLevelEnabled(): boolean {
        if (this._trans) {
            return this._trans.autoLevelEnabled;
        }
        return false;
    }

    public get levels(): AbrLevel[] {
        if (this._trans) {
            return this._trans.levels;
        }
        return [];
    }

    public get nextLevel(): number {
        if (this._trans) {
            return this._trans.nextLevel;
        }
        return 0;
    }

    public set nextLevel(value: number) {
        if (!this._verifyLevel(value) || !this._trans) {
            this.emit(LasEvents.LEVEL_SWITCH_FAILED, { level: value });
            return;
        }
        this._trans.nextLevel = value;
    }

    public get currentLevel(): number {
        if (this._trans) {
            return this._trans.currentLevel;
        }
        return 0;
    }

    public set currentLevel(value: number) {
        if (!this._verifyLevel(value) || !this._trans) {
            this.emit(LasEvents.LEVEL_SWITCH_FAILED, { level: value });
            return;
        }

        if (value === -1) {
            this._trans.nextLevel = value;
        } else {
            this._stat = STAT.SELECT_BITRATE;
            this._bufferThreshold = BUFFER_THRESHOLD_DEFAULT;
            this._seekOnCanplay = true;
            if (this._mse) {
                this._mse.flush();
            }
            this._trans.currentLevel = value;
        }
    }

    public get startLevel(): number {
        return typeof this._startLevel === 'undefined' ? -1 : this._startLevel;
    }

    public set startLevel(value: number) {
        this._startLevel = value;
    }

    public get monitorData() {
        if (this._monitor) {
            return this._monitor.data;
        }
        return;
    }

    private _bindVideoEvents(): void {
        if (this._video) {
            this._video.addEventListener('loadeddata', this._onVideoLoadeddata);
            this._video.addEventListener('canplay', this._onVideoCanplay);
            this._video.addEventListener('waiting', this._onVideoWaiting);
            this._video.addEventListener('playing', this._onVideoPlaying);
            this._video.addEventListener('play', this._onVideoPlay);
            this._video.addEventListener('error', this._onVideoError);
            this._video.addEventListener('ended', this._onVideoEnded);
        }
    }

    private _unbindVideoEvents(): void {
        if (this._video) {
            this._video.removeEventListener('loadeddata', this._onVideoLoadeddata);
            this._video.removeEventListener('canplay', this._onVideoCanplay);
            this._video.removeEventListener('waiting', this._onVideoWaiting);
            this._video.removeEventListener('playing', this._onVideoPlaying);
            this._video.removeEventListener('play', this._onVideoPlay);
            this._video.removeEventListener('error', this._onVideoError);
            this._video.removeEventListener('ended', this._onVideoEnded);
        }
    }
    /**
     * mediasource
     * @param {MSEController} mediaSource MSEController
     */
    private _unbindMediaSourceEvent(mediaSource: MSEController): void {
        mediaSource.removeAllListeners();
    }

    private _onVideoPlay = (): void => {
        if (!this._error) {
            if (!this._trans) {
                this.load();
            }
        }
    };

    private _onVideoEnded = (): void => {
        this._monitor.onEnd();
        if (this._mse) {
            this._mse.flush();
        }
    };

    /**
     * new mediaSource
     */
    private _initMSE(video: HTMLVideoElement) {
        this._mse = new MSEController(this._config);
        this._mse.attach(video);
        this._media.attachMSE(this._mse);

        this._mse.on(LasEvents.ERROR, (errorMessage: any) => {
            this._onError(errorMessage);
        });
        this._mse.on('updateend', () => {
            if (this._seekOnUpdateEnd && video.buffered.length) {
                Log.i(this.tag, 'seek on updateend');
                this._internalSeek(video.buffered.start(0));
                this._seekOnUpdateEnd = false;
            }
        });
        this._mse.on('resetDone', () => {
            this._seekOnUpdateEnd = true;
        });
    }

    /**
     * 
     */
    private _load() {
        this._loadStopped = false;
        this._error = false;
        this._stat = STAT.INIT;
        this._bufferThreshold = BUFFER_THRESHOLD_DEFAULT;
        this._nextLevel = [];
        this._media.reset();

        this._monitor.onLoad();
        if (this._trans) {
            this._destroyTrans();
        }
        if (this._mse.hasSourceBuffer() || (this._video && this._video.error)) {
            this._resetMSE();
        }
        this._initTrans();
        if (this._trans) {
            this._trans.loadSource();
        }
    }

    /**
     * reset MSE
     */
    private _resetMSE = () => {
        this._seekOnUpdateEnd = false;
        if (this._video) {
            Log.i(this.tag, 'rebuild mse');
            URL.revokeObjectURL(this._video.src);
            this._video.src = '';
            this._video.removeAttribute('src');
            this._destroyMSE();
            this._initMSE(this._video);
        }
    }

    private _verifyLevel(value: number): boolean {
        return !!(this._trans &&
            this._trans.levels.length > 0 &&
            value < this._trans.levels.length &&
            value >= -1 &&
            this._video && !this._video.ended);
    }

    private _transmuxerEvent(trans: TransFlv): void {
        const mse = this._mse;
        trans.on(LasEvents.PARSING_DATA, data => {
            if (mse) {
                mse.mediaSegment(data);
            }
            if (this._monitor) {
                this._monitor.onSegment({
                    type: data.type,
                    byteLength: data.data.byteLength,
                    dts: Math.floor(data.startDTS * 1000),
                    duration: Math.floor(data.duration * 1000),
                    framesInfo: data.framesInfo
                });
            }
        });

        trans.on(LasEvents.PARSING_INIT_SEGMENT, data => {
            const video = data.tracks.video,
                audio = data.tracks.audio,
                audiovideo = data.tracks.audiovideo;
            const info: any = {
                segments: [],
                audiovideo: !!audiovideo,
                hasVideo: !!(video || audiovideo),
                hasAudio: !!(audio || audiovideo)
            };
            for (const key in data.tracks) {
                const track = data.tracks[key];
                Object.assign(info, track.metadata);
                if (track.initSegment) {
                    info.segments.push({ type: key, data: new Uint8Array(track.initSegment) });
                }
            }
            info.videoCodec = video ? video.codec : null;
            info.audioCodec = audio ? audio.codec : null;
            this._audioCodec = data.manifestAudioCodec || info.audioCodec;
            const mediaInfo = Object.assign({}, info);
            delete mediaInfo.segments;
            this._monitor.onSegmentInit(mediaInfo);
            this.emit(LasEvents.MEDIA_INFO, mediaInfo);
            this._mediaInfo = mediaInfo;
            if (mse) {
                mse.trackInfo(info);
            }
        });

        trans.on(LasEvents.ERROR, errorMessage => {
            this._onError(errorMessage);
        });

        trans.on(LasEvents.LOAD_END, () => {
            if (mse) {
                mse.endOfData();
            }
            this.emit(LasEvents.LOAD_END);
        });
        trans.on(LasEvents.LEVEL_SWITCH_FAILED, data => {
            this.emit(LasEvents.LEVEL_SWITCH_FAILED, data);
        });
        trans.on(LasEvents.LEVEL_SWITCHING, data => {
            if (!data.smooth && this._mse) {
                this._mse.flush();
            }
            this.emit(LasEvents.LEVEL_SWITCHING, { level: data.level });
            this._nextLevel = this._nextLevel
                .sort((a, b) => {
                    return a.startSec - b.startSec;
                })
                .filter(value => {
                    return value.startSec < data.startSec;
                });
            this._nextLevel.push(data);
        });
        trans.on(LasEvents.BUFFER_FLUSHING, data => {
            if (this._mse && this._video) {
                this._mse.flush(data.startSec, this._video.duration);
            }
        });
        trans.on(LasEvents.SCRIPT_PARSED, data => {
            this.emit(LasEvents.SCRIPT_PARSED, data);
        });
        trans.on(LasEvents.MANIFEST_PARSED, data => {
            if (typeof this._playingLevel === 'number') {
                trans.currentLevel = this._playingLevel;
                return;
            }

            if (typeof this._startLevel === 'number') {
                trans.currentLevel = this._startLevel;
            }
            data = Object.assign({ levels: this.levels.slice(0), currentLevel: this.currentLevel }, data);
            this._playingLevel = trans.currentLevel;
            Log.i(this.tag, LasEvents.MANIFEST_PARSED, data);
            this.emit(LasEvents.MANIFEST_PARSED, data);
        });
        trans.on(LasEvents.REPORT, data => {
            if (this._monitor) {
                this._monitor.onReport(data);
            }
        });
    }

    private _internalSeek(time: number): void {
        if (this._video) {
            this._video.currentTime = time;
        }
    }

    /**
     * Check buffer length regularly and apply various policies to avoid buffering
     */
    private _mainLoop = (): void => {
        const EPS = 1e-3;
        const video = this._video;

        /**
         * Ibufferbuffer
         * seekbuffer
         * 1. bufferseekWAITING_STAT.WAITING && !video.seeking
         * 2. bufferseekWAITING_SEEK || WAITING_SELECT_BITRATE || WAITING_INIT
         * 3. buffer
         */
        if (video &&
            ((this._stat === STAT.WAITING && !video.seeking) ||
                this._stat === STAT.INIT ||
                this._stat === STAT.SEEK ||
                this._stat === STAT.SELECT_BITRATE) &&

            this._mse &&
            !this._mse.hasCleanUpTask() &&
            !video.ended
        ) {
            const currentTime = video.currentTime;
            const currentBuffer = this._media.currentBuffer(currentTime);
            let jumpTo: number | undefined = undefined;
            // buffer
            if (!currentBuffer || currentBuffer.end - currentTime < 1) {
                const nextBuffer = this._media.nextBuffer(currentTime);
                if (nextBuffer) {
                    Log.i(this.tag, 'try fix block-A');
                    jumpTo = nextBuffer.start;
                }
            } else if (video.buffered.length > 1 && currentBuffer.end - currentTime > 1) {
                // buffer
                Log.i(this.tag, 'try fix block-B');
                jumpTo = currentBuffer.start;
            }

            if (jumpTo) {
                jumpTo = jumpTo + (Browser.safari ? 0.3 : EPS);
                this._internalSeek(jumpTo);
                Log.i(this.tag, `jump to ${jumpTo}`);
            }
        }
        if (this._nextLevel.length) {
            this._checkLevelChange();
        }
    };

    private _onVideoLoadeddata = (): void => {
        Log.i(this.tag, 'loadeddata');
        this._monitor.onLoadeddata();
    }

    /**
     * canplay event listener
     */
    private _onVideoCanplay = (): void => {
        Log.v(this.tag, `canplay ${!!this._stat}`);
        this._monitor.onCanplay();
        if (this._video && this._stat !== STAT.NONE) {
            this._stat = STAT.NONE;
            this._detectSeekOnCanplay();
            this._checkLevelChange();
            if (!this._video.paused) {
                // waitingplaying
                this._onVideoPlaying();
            }
        }
    };

    private _detectSeekOnCanplay(): void {
        if (this._video && this._seekOnCanplay && this._mse && !this._mse.hasCleanUpTask()) {
            let EPS = 1 / 30;
            const mediaInfo = this._mediaInfo;
            if (mediaInfo && mediaInfo.fps && mediaInfo.fps > 0) {
                EPS = 1 / mediaInfo.fps;
            }
            if (this._media.isTimeinBuffered(this._video.currentTime + EPS)) {
                Log.i(this.tag, `seek on canplay ${this._video.currentTime} + ${EPS}`);
                this._seekOnCanplay = false;
                this._internalSeek(this._video.currentTime + EPS);
            }
        }
    }

    /**
     * 'waiting' event listener
     */
    private _onVideoWaiting = (): void => {
        if (!this._video) {
            return;
        }
        this._stat = this._stat || STAT.WAITING;
        const block = !this._video.seeking && this._stat === STAT.WAITING;
        if (block) {
            this._bufferThreshold = Math.min(this._bufferThreshold + BUFFER_THRESHOLD_STEP, BUFFER_THRESHOLD_MAX);
        }
        if (block) {
            Log.i(this.tag, 'waiting currentTime:', this._video.currentTime);
        }
        this._monitor.onWaiting(block);
    };

    /**
     * 'playing' event listener
     * playing
     */
    private _onVideoPlaying = (): void => {
        Log.i(this.tag, 'playing');
        if (!this._error) {
            this._stat = STAT.NONE;
            this._monitor.onPlaying();
        }
    };

    private _onVideoError = (error: any): void => {
        Log.e(this.tag, 'video error', error);
        if (this._error) {
            return;
        }
        const now = performance.now();
        if (!this._recoverMediaErrorTime || now - this._recoverMediaErrorTime > 3000) {
            this._recoverMediaErrorTime = now;
            this._config.gopRemux = true;
            this._recoverMediaError();
            return;
        }
        if (!this._audioCodecSwap && this._audioCodec) {
            this._audioCodecSwap = true;
            this._recoverSwapAudioCodec();
            this._recoverMediaError();
            return;
        }

        let reason = 'video error';
        if (this._video && this._video.error) {
            reason += ` code:${this._video.error.code} message:${this._video.error.message}`;
        }
        this._onError({
            type: ErrorTypes.MEDIA_ERROR,
            details: ErrorDetails.VIDEO_ERROR,
            fatal: true,
            reason
        });
    };

    /**
     * 
     * @param {*} data 
     */
    private _onError(data: any): void {
        Log.i(this.tag, `on error ${JSON.stringify(data)}`);
        const errInfo: any = {};
        if (data.type === ErrorTypes.NETWORK_ERROR) {
            errInfo.statusCode = data.statusCode;
            errInfo.url = data.url;
        }
        if (data.fatal) {
            const errorCode = getErrorCode(data.details, data.reason, data.statusCode || 0);
            this.stopLoad();
            this._stopMainTimer();
            if (data.details === ErrorDetails.VIDEO_ERROR || (this._video && this._video.error)) {
                this._destroyMSE();
            }
            errInfo.code = errorCode;
            errInfo.type = data.type;
            errInfo.reason = data.reason;
            if (!this._error) {
                this._error = errInfo;
                this.emit(LasEvents.ERROR, errInfo);
            }
        }
    }

    private _startMainTimer(): void {
        if (this._mainTimer === null) {
            this._mainTimer = setInterval(this._mainLoop, MAIN_TIMER_INTERVAL);
        }
    }

    private _stopMainTimer(): void {
        if (this._mainTimer) {
            clearInterval(this._mainTimer);
            this._mainTimer = null;
        }
    }

    private _checkLevelChange(): void {
        const d = this._nextLevel[0];
        if (this._video && d && this._video.currentTime >= d.startSec && this._media.isTimeinBuffered(this._video.currentTime)) {
            this.emit(LasEvents.LEVEL_SWITCHED, { level: d.level });
            this._playingLevel = d.level;
            this._nextLevel.shift();
        }
    }

    private _stopVideo(): void {
        if (this._video) {
            URL.revokeObjectURL(this._video.src);
            this._video.src = '';
            this._video.removeAttribute('src');
            this._destroyTrans();
            this._destroyMSE();
        }
    }

    private _destroyTrans(): void {
        if (this._trans) {
            this._trans.removeAllListeners();
            this._trans.destroy();
            this._trans = undefined;
        }
    }

    private _destroyMSE(): void {
        if (this._mse) {
            this._unbindMediaSourceEvent(this._mse);
            this._mse.destroy();
        }
    }

    private _initTrans(): void {
        this._trans = new TransFlv(this._config, this._media);
        this._transmuxerEvent(this._trans);
        this._trans.init();
    }

    private _initMonitor(): void {
        if (!this._monitor) {
            this._monitor = new Monitor(this._media);
            this._monitor.on(LasEvents.HEARTBEAT, value => {
                this.emit(LasEvents.HEARTBEAT, value);
            })
        }
    }

    private _stopMonitor(): void {
        if (this._monitor) {
            this._monitor.destroy();
            this._monitor.removeAllListeners();
        }
    }

    /**
     * audio codec string
     */
    private _recoverSwapAudioCodec(): void {
        let audioCodec = this._audioCodec;
        if (audioCodec.indexOf('mp4a.40.5') !== -1) {
            audioCodec = 'mp4a.40.2';
        } else {
            audioCodec = 'mp4a.40.5';
        }
        this._config.audioCodec = audioCodec;
        this._config.audioCodecSwap = true;
    }

    /**
     * video errorvideo
     */
    private _recoverMediaError(): void {
        if (!this._video) {
            return;
        }
        this._nextLevel = [];

        this._stopVideo();
        this._initMSE(this._video);

        this._initTrans();
        if (this._trans) {
            this._trans.loadSource();
        }

    }
}
