import { LasConfig } from './types/core';
import Browser from './utils/browser';
import { Log, LOG_LEVEL } from './utils/log';

const CLEANUP_DURATION_STEP = 10;

const URL_REG = new RegExp('^(http|https)://');

const DEFAULT_CONFIG = {
    webWorker: true, // webworker
    autoCleanupMaxBackwardDuration: 60, // sourcebuffer
    autoCleanupMinBackwardDuration: 15, // sourcebuffer
    appendErrorMaxRetry: 3, // mse append
    credentials: false, // cookie
    defaultSpts: -2000,
    debug: LOG_LEVEL.LEVEL_ERROR,

    connectionTimeout: 10000, // 
    transmissionTimeout: 30000, // 
    stretchShortVideoTrack: false,
    maxBufferHole: 0.5,
    maxAudioFramesDrift: 5, // used by mp4-remuxer
};

class ConfigHelper {
    /**
     * config
     * @param {object} userConfig config
     * @returns {object} config
     */
    static processConfig(userConfig: any): LasConfig {
        const config: LasConfig = Object.assign({}, DEFAULT_CONFIG);
        Object.assign(config, userConfig);
        ConfigHelper.setSrc(config);

        if (!(window as any).Worker) {
            config.webWorker = false;
        }
        if (config.debug) {
            Log.level(config.debug);
        }
        ConfigHelper.detectStreamingMux(config);
        config.autoCleanupMaxBackwardDuration = Math.max(
            config.autoCleanupMaxBackwardDuration,
            config.autoCleanupMinBackwardDuration + CLEANUP_DURATION_STEP
        );
        return config;
    }

    static detectStreamingMux(config: LasConfig) {
        config.gopRemux = Browser.safari || (Browser.opera && Browser.version.major < 37);
    }

    /**
     * configsrc
     * @param {object} config config
     * @param {*} src srcmanifest
     */
    static setSrc(config: LasConfig, src: any = null) {
        if (src) {
            config.src = src;
            config.manifest = '';
        }
        if (typeof config.src === 'string' && !URL_REG.test(config.src)) {
            try {
                config.src = JSON.parse(config.src);
            } catch (e) {
                config.manifest = '';
            }
        }
        if (typeof config.src === 'object') {
            config.manifest = config.src;
            config.src = '';
        }
    }
}

export { DEFAULT_CONFIG, ConfigHelper };

