import LasEvents from '../../core/events';
import Observer from '../../core/observer';
import Mp4Remuxer from '../../remux/mp4-remuxer';
import { FLVTransConfig } from '../../types/core';
import FlvDemuxer from './flv-demuxer';
import { FlvTag } from './flv';

export interface FlvExtraData {
    remuxId: number;
    bitrate?: number;
}
class FlvDemuxerInline {
    private tag: string = 'Flv';
    private _observer: Observer;
    private _config: FLVTransConfig;
    private _extraData: FlvExtraData;

    private _demuxer!: FlvDemuxer;
    private _remuxer!: Mp4Remuxer;

    private _bitrate: number;
    private _vendor: string;

    constructor(observer: Observer, config: FLVTransConfig, extraData: FlvExtraData, vendor: string) {
        this._observer = observer;
        this._config = config;

        this._bitrate = this._config.bitrate as number;
        this._extraData = extraData;
        this._vendor = vendor;
    }

    public init(): void {
        const config = this._config,
            observer = this._observer;

        const remuxer = (this._remuxer = new Mp4Remuxer(observer, config, { mp4: true, mpeg: false, mp3: false }));
        const demuxer = (this._demuxer = new FlvDemuxer(observer, remuxer, config));
        remuxer.setExtra(this._extraData);
        demuxer.duration = config.duration || 0;
    }

    public flvHead(hasAudio: boolean, hasVideo: boolean): void {
        this._demuxer.flvHead(hasAudio, hasVideo);
    }

    public append(tags: FlvTag[], timeOffset: number, discontinuity: boolean, contiguous: boolean, accurateTimeOffset: boolean) {
        if (discontinuity) {
            this._demuxer.resetInitSegment();
            this._remuxer.resetInitSegment();
            this._demuxer.resetTimeStamp();
            this._remuxer.resetTimeStamp();
        }
        this._demuxer.append(tags, timeOffset, contiguous, accurateTimeOffset);
    }

    public end(): void {
        this._demuxer.flush();
        this._remuxer.flush();
        this._observer.trigger(LasEvents.LOAD_END);
    }

    public flush(): void {
        if (this._demuxer) {
            this._demuxer.flush();
        }
    }

    public setExtra(data: FlvExtraData): void {
        this._extraData = data;
        this._extraData.bitrate = this._bitrate;
        if (this._remuxer) {
            this._remuxer.setExtra(this._extraData);
        }
    }

    public destroy(): void { }

}

export default FlvDemuxerInline;
