import Browser from '../utils/browser';
import FetchLoader from './fetch';
import { XHR } from './xhr';
import { Log } from '../utils/log';
import {
    IBaseLoader,
    ILoaderContext,
    IBaseLoaderCallback,
    ILoaderCallback,
    ILoaderStats,
    ILoaderConfig,
    ILoader
} from '../types/io';

let ChunkLoader: any;
let index: number = 0;
/**
 * 
 * @param  {object} video config
 */
export default class Loader<T extends ILoaderContext> implements ILoader<T> {
    public tag: string = 'loader';
    public context!: T;
    private _loader: IBaseLoader<T> | null;
    protected _callbacks: ILoaderCallback<T> | null = null;
    protected _config: ILoaderConfig;
    private _loaderCallback!: IBaseLoaderCallback<T>;
    protected _stats!: ILoaderStats;
    protected _retryDelay: number = 0;
    private _loading: boolean = false;
    private _aborted: boolean = false;
    private _requestTimeout: any;
    private _transTimer: any;
    private _retryTimeout: any;
    protected _rangeStart?: number;
    private _continuedTransmissionRetry: boolean = false;
    private _progressTime: number = 0;

    /**
     * Loader
     * @param useFetch fetch
     * @returns class
     */
    _getInternalLoader(useFetch: boolean) {
        if (typeof ChunkLoader !== 'undefined') {
            return ChunkLoader;
        }
        ChunkLoader = null;
        // msxhrfetch
        if (Browser.msie || Browser.msedge) {
            if (useFetch) {
                if (FetchLoader.isSupport()) {
                    ChunkLoader = FetchLoader;
                }
                return ChunkLoader;
            }
            if (XHR.isSupportChunk()) {
                ChunkLoader = XHR;
            } else if (FetchLoader.isSupport()) {
                ChunkLoader = FetchLoader;
            }
        } else if (FetchLoader.isSupport()) {
            ChunkLoader = FetchLoader;
        } else if (XHR.isSupportChunk()) {
            ChunkLoader = XHR;
        }
        return ChunkLoader;
    }

    constructor() {
        this.tag = 'loader';
        index++;
        this._loader = null;
        this._config = {
            useFetch: false,
            connectionTimeout: 0,
            transmissionTimeout: 0,
            maxRetry: 0,
            retryDelay: 0
        };
        this._loaderCallback = {
            onConnect: this._onConnect.bind(this),
            onProgress: this._onProgress.bind(this),
            onEnd: this._onEnd.bind(this),
            onError: this._onError.bind(this)
        };
    }

    load(context: T, callbacks: ILoaderCallback<T>, config: ILoaderConfig) {
        this._init(context, callbacks, config);
        this._loadInternal();
    }

    /**
     * destory
     */
    destroy() {
        this._stopTimer();
        this._abortInternal();
        this._destroyLoader();
        this._callbacks = null;
    }

    _init(context: T, callbacks: ILoaderCallback<T>, config: ILoaderConfig) {
        this.context = context;
        this._rangeStart = context.rangeStart
        this._callbacks = callbacks;
        this._config = config || this._config;

        this._stats = {
            id: '',
            trequest: performance.now(),
            retry: 0,
            loaded: 0,
            code: 0,
            output: 0,
            tfirst: 0,
            tload: 0,
            total: 0,
            tsload: 0,
            tstart: 0,
            fatal: false,
            text: ''
        };
        if (this._config.retryDelay) {
            this._retryDelay = this._config.retryDelay;
        }
    }

    /**
     * loader
     */
    _destroyLoader() {
        if (this._loader) {
            this._loader.destroy();
            this._loader = null;
        }
    }

    /**
     * xhr
     */
    _loadInternal() {
        this._loading = true;
        this._aborted = false;
        const stats = this._stats;
        stats.code = 0;
        stats.tfirst = 0;
        stats.loaded = 0;
        if (this._retryTimeout) {
            clearTimeout(this._retryTimeout);
            this._retryTimeout = null;
        }
        if (this.context.progress) {
            this._loader = new (this._getInternalLoader(!!this._config.useFetch))();
        } else {
            this._loader = new XHR();
        }
        if (!this._loader) {
            return;
        }
        stats.id = `${this._loader.tag}-${index}`;
        // 
        if (this._config.connectionTimeout) {
            this._requestTimeout = setTimeout(this._onTimeout.bind(this), this._config.connectionTimeout);
        }
        if (this.context.rangeEnd) {
            this.context.range = 'bytes=' + this.context.rangeStart + '-' + (this.context.rangeEnd - 1);
        } else if (this.context.rangeStart) {
            this.context.range = 'bytes=' + this.context.rangeStart + '-';
        }
        stats.tsload = Date.now();
        stats.tstart = performance.now();
        this._loader.load(this.context, this._loaderCallback);
    }

    _abortInternal() {
        if (this._callbacks && this._callbacks.onAbort && !this._aborted && this._loading) {
            this._callbacks.onAbort(this.context, this._stats);
        }
        this._aborted = true;
        if (this._loader) {
            this._loader.abort();
        }
    }

    abort() {
        this._stopTimer();
        this._abortInternal();
    }

    /**
     * 
     * 
     */
    _stopTimer() {
        if (this._requestTimeout) {
            clearTimeout(this._requestTimeout);
            this._requestTimeout = null;
        }
        if (this._retryTimeout) {
            clearTimeout(this._retryTimeout);
            this._retryTimeout = null;
        }
        this._stopTransmissionTimer();
    }

    _onConnect(status: number) {
        if (this._requestTimeout) {
            clearTimeout(this._requestTimeout);
            this._requestTimeout = null;
        }
        this._startTransmissionTimer();
        this._stats.code = status;
        this._stats.tfirst = Math.max(this._stats.trequest, performance.now());
    }
    _onProgress(context: T, chunk: ArrayBuffer) {
        const stats = this._stats;
        this._progressTime = performance.now();
        // rangerange
        if (!this._continuedTransmissionRetry && stats.output > stats.loaded) {
            const size = stats.loaded + chunk.byteLength - stats.output;
            if (size > 0) {
                const tmp = chunk.slice(stats.output - stats.loaded);
                this._callProgress(this.context, tmp, stats);
            }
        } else {
            this._callProgress(this.context, chunk, stats);
        }
        stats.loaded += chunk.byteLength;
    }
    _callProgress(context: T, chunk: ArrayBuffer, stats: ILoaderStats) {
        this._stats.output += chunk.byteLength;
        if (this._callbacks && this._callbacks.onProgress) {
            this._callbacks.onProgress(context, chunk, stats);
        }
    }
    _onEnd(context: T, responseData: string | ArrayBuffer | null) {
        this._stopTimer();
        const stats = this._stats;
        if (responseData) {
            if (typeof responseData === 'string') {
                stats.total = stats.loaded = responseData.length || 0;
            } else {
                stats.total = stats.loaded = responseData.byteLength || 0;
            }
        } else {
            stats.total = stats.loaded;
        }
        stats.tload = Math.max(stats.tfirst, performance.now());
        this._loading = false;
        stats.output = 0;
        if (this._callbacks) {
            this._callbacks.onEnd(context, responseData, stats);
        }
    }

    _onError(e: Error) {
        Log.i(this.tag, e);
        this._stopTimer();
        this._destroyLoader();
        const stats = this._stats;
        const config = this._config;
        this._loading = false;

        stats.fatal = !config.maxRetry || (stats.retry >= config.maxRetry || !config.maxRetry);
        stats.text = e.message || 'load error';
        if (this._callbacks && this._callbacks.onError) {
            this._callbacks.onError(this.context, stats);
        }
        if (stats.fatal) {
            return;
        }
        // range
        if (this._stats.output && this._stats.loaded > 0) {
            if (this._rangeStart) {
                this.context.rangeStart = this._rangeStart + this._stats.output;
            } else {
                this.context.rangeStart = this._stats.output;
            }
            this._continuedTransmissionRetry = true;
        } else {
            this.context.rangeStart = this._rangeStart;
            this._continuedTransmissionRetry = false;
        }
        stats.retry++;
        if (this._callbacks) {
            if (this._retryDelay) {
                this._retryTimeout = setTimeout(this._loadInternal.bind(this), this._retryDelay);
                this._retryDelay = 2 * this._retryDelay;
            } else {
                this._loadInternal();
            }
        }
    }

    _onTimeout() {
        this._loading = false;
        this._abortInternal();
        const e = new Error('timeout');
        this._onError(e);
    }

    // 
    _startTransmissionTimer() {
        this._stopTransmissionTimer();
        this._progressTime = performance.now();
        let timeout = this._config.transmissionTimeout || 0;
        if (timeout) {
            this._transTimer = setInterval(() => {
                if (performance.now() - this._progressTime > timeout) {
                    this._onTimeout();
                }
            }, 1000);
        }
    }

    _stopTransmissionTimer() {
        if (this._transTimer) {
            clearInterval(this._transTimer);
            this._transTimer = null;
        }
    }
}
