/* eslint-disable */
import decodeUTF8 from '../../utils/decodeUTF8';
import { Log } from '../../utils/log';

let tag: string = 'AMF';

export default class AMF {
    /**
     * metadata
     */
    static parseMetadata(arr: ArrayBuffer) {
        let data: any = {};
        try {
            let name = AMF.parseScript(arr, 0);
            let value = AMF.parseScript(arr, name.size);
            data[name.data] = value.data;
        } catch (e) {
            Log.e('AMF', e.toString());
        }
        return data;
    }

    static parseObject(arrayBuffer: ArrayBuffer, dataOffset: number) {
        let name = AMF.parseString(arrayBuffer, dataOffset);
        let value = AMF.parseScript(arrayBuffer, dataOffset + name.size);
        let isObjectEnd = value.objectEnd;

        return {
            data: {
                name: name.data,
                value: value.data
            },
            size: value.size,
            objectEnd: isObjectEnd
        };
    }

    static parseVariable(arrayBuffer: ArrayBuffer, dataOffset: number) {
        return AMF.parseObject(arrayBuffer, dataOffset);
    }
    static parseLongString(arrayBuffer: ArrayBuffer, dataOffset: number) {
        let v = new DataView(arrayBuffer, dataOffset);
        let length = v.getUint32(0);

        let str;
        if (length > 0) {
            str = decodeUTF8(new Uint8Array(arrayBuffer, dataOffset + 4, length));
        } else {
            str = '';
        }

        return {
            data: str,
            size: 4 + length
        };
    }
    static parseDate(arrayBuffer: ArrayBuffer, dataOffset: number) {
        let v = new DataView(arrayBuffer, dataOffset);
        let timestamp = v.getFloat64(0);
        let localTimeOffset = v.getInt16(8);
        timestamp += localTimeOffset * 60 * 1000; // get UTC time

        return {
            data: new Date(timestamp),
            size: 8 + 2
        };
    }
    static parseString(arrayBuffer: ArrayBuffer, dataOffset: number) {
        let v = new DataView(arrayBuffer, dataOffset);
        let length = v.getUint16(0);
        let str;
        if (length > 0) {
            str = decodeUTF8(new Uint8Array(arrayBuffer, dataOffset + 2, length));
        } else {
            str = '';
        }
        return {
            data: str,
            size: 2 + length
        };
    }

    static parseScript(arr: ArrayBuffer, dataOffset: number) {
        let dataSize = arr.byteLength;
        let offset = dataOffset;
        // let uint8 = new Uint8Array(arr);
        let buffer = arr;
        let dv = new DataView(buffer, 0);

        let value: any = null;
        let type = dv.getUint8(offset);
        offset += 1;
        let objectEnd = false;

        switch (type) {
            case 0: // Number(Double) type
                value = dv.getFloat64(offset);
                offset += 8;
                break;
            case 1: {
                // Boolean type
                let b = dv.getUint8(offset);
                value = !!b;
                offset += 1;
                break;
            }
            case 2: {
                // String type
                let amfstr = AMF.parseString(buffer, offset);
                value = amfstr.data;
                offset += amfstr.size;
                break;
            }
            case 3: {
                // Object(s) type
                value = {};
                let terminal = 0; // workaround for malformed Objects which has missing ScriptDataObjectEnd
                if ((dv.getUint32(dataSize - 4) & 0x00ffffff) === 9) {
                    terminal = 3;
                }
                while (offset < dataSize - 4) {
                    // 4 === type(UI8) + ScriptDataObjectEnd(UI24)
                    let amfobj = AMF.parseObject(buffer, offset);

                    if (amfobj.objectEnd) {
                        break;
                    }
                    value[amfobj.data.name] = amfobj.data.value;
                    // dataOffset += amfobj.size;
                    offset = amfobj.size;
                }
                if (offset <= dataSize - 3) {
                    let marker = dv.getUint32(offset - 1) & 0x00ffffff;
                    if (marker === 9) {
                        offset += 3;
                    }
                }
                break;
            }
            case 8: {
                // ECMA array type (Mixed array)
                value = {};
                // dataOffset += 1;
                offset += 4; // ECMAArrayLength(UI32)
                let terminal = 0; // workaround for malformed MixedArrays which has missing ScriptDataObjectEnd
                if ((dv.getUint32(dataSize - 4) & 0x00ffffff) === 9) {
                    terminal = 3;
                }
                while (offset < dataSize - 8) {
                    // 8 === type(UI8) + ECMAArrayLength(UI32) + ScriptDataVariableEnd(UI24)
                    let amfvar = AMF.parseVariable(buffer, offset);

                    if (amfvar.objectEnd) {
                        break;
                    }
                    value[amfvar.data.name] = amfvar.data.value;
                    offset = amfvar.size;
                }
                if (offset <= dataSize - 3) {
                    let marker = dv.getUint32(offset - 1) & 0x00ffffff;
                    if (marker === 9) {
                        offset += 3;
                    }
                }
                break;
            }
            case 9: // ScriptDataObjectEnd
                value = undefined;
                offset = 1;
                objectEnd = true;
                break;
            case 10: {
                // Strict array type
                // ScriptDataValue[n]. NOTE: according to video_file_format_spec_v10_1.pdf
                value = [];
                let strictArrayLength = dv.getUint32(offset);
                offset += 4;
                for (let i = 0; i < strictArrayLength; i++) {
                    let val = AMF.parseScript(buffer, offset);
                    value.push(val.data);
                    offset = val.size;
                }
                break;
            }
            case 11: {
                // Date type
                let date = AMF.parseDate(buffer, offset + 1);
                value = date.data;
                offset += date.size;
                break;
            }
            case 12: {
                // Long string type
                let amfLongStr = AMF.parseString(buffer, offset + 1);
                value = amfLongStr.data;
                offset += amfLongStr.size;
                break;
            }
            default:
                // ignore and skip
                offset = dataSize;
                Log.v(tag, 'AMF', 'Unsupported AMF value type ' + type);
        }
        return {
            data: value,
            size: offset,
            objectEnd
        };
    }
}
