import LasEvents from '../core/events';
import Observer from '../core/observer';
import { ErrorTypes, ErrorDetails } from '../core/errors';
import { Log } from '../utils/log';

export interface ASCInfo {
    objectType: number;
    sampleingIndex: number;
    chanelConfig: number;
}

export interface ASCConfig {
    config: number[];
    samplerate: number;
    channelCount: number;
    codec: string;
    manifestCodec: string;
}

export function getAudioConfig(observer: Observer, info: ASCInfo, audioCodec: string = ''): ASCConfig | null {
    let extensionSampleingIndex: number, // :int
        config: Array<number>,
        objectType = info.objectType,
        sampleingIndex = info.sampleingIndex,
        chanelConfig = info.chanelConfig;
    const userAgent = navigator.userAgent.toLowerCase(),
        manifestCodec = audioCodec,
        sampleingRates = [96000, 88200, 64000, 48000, 44100, 32000, 24000, 22050, 16000, 12000, 11025, 8000, 7350];

    if (sampleingIndex < 0 || sampleingIndex >= sampleingRates.length) {
        observer.trigger(LasEvents.ERROR, {
            type: ErrorTypes.MUX_ERROR,
            details: ErrorDetails.PARSING_ERROR,
            fatal: true,
            reason: `invalid sampling index:${sampleingIndex}`
        });
        return null;
    }
    if (chanelConfig < 0 || chanelConfig >= 8) {
        observer.trigger(LasEvents.ERROR, {
            type: ErrorTypes.MUX_ERROR,
            details: ErrorDetails.PARSING_ERROR,
            fatal: true,
            reason: `invalid chanelConfig:${chanelConfig}`
        });
        return null;
    }

    Log.v(
        `manifest codec:${audioCodec},data:type:${objectType},sampleingIndex:${sampleingIndex}[${
            sampleingRates[sampleingIndex]
        }Hz],channelConfig:${chanelConfig}`
    );
    // firefox: freq less than 24kHz = AAC SBR (HE-AAC)
    if ((/firefox/i).test(userAgent)) {
        if (sampleingIndex >= 6) {
            objectType = 5;
            config = new Array(4);
            // HE-AAC uses SBR (Spectral Band Replication) , high frequencies are constructed from low frequencies
            // there is a factor 2 between frame sample rate and output sample rate
            // multiply frequency by 2 (see table below, equivalent to substract 3)
            extensionSampleingIndex = sampleingIndex - 3;
        } else {
            objectType = 2;
            config = new Array(2);
            extensionSampleingIndex = sampleingIndex;
        }
        // Android : always use AAC
    } else if (userAgent.indexOf('android') !== -1) {
        objectType = 2;
        config = new Array(2);
        extensionSampleingIndex = sampleingIndex;
    } else {
        /*  for other browsers (Chrome/Vivaldi/Opera ...)
        always force audio type to be HE-AAC SBR, as some browsers do not
        support audio codec switch properly (like Chrome ...)
    */
        objectType = 5;
        config = new Array(4);
        // if (manifest codec is HE-AAC or HE-AACv2) OR (manifest codec not specified AND frequency less than 24kHz)
        if (
            audioCodec && (audioCodec.indexOf('mp4a.40.29') !== -1 || audioCodec.indexOf('mp4a.40.5') !== -1)
            || !audioCodec && sampleingIndex >= 6
        ) {
            // HE-AAC uses SBR (Spectral Band Replication) , high frequencies are constructed from low frequencies
            // there is a factor 2 between frame sample rate and output sample rate
            // multiply frequency by 2 (see table below, equivalent to substract 3)
            extensionSampleingIndex = sampleingIndex - 3;
        } else {
            /* if (manifest codec is AAC) AND (frequency less than 24kHz AND nb channel is 1)
                OR(manifest codec not specified and mono audio)
                Chrome fails to play back with low frequency AAC LC mono when initialized with HE-AAC.
                This is not a problem with stereo.
            */
            if (
                audioCodec
                    && audioCodec.indexOf('mp4a.40.2') !== -1
                    && (sampleingIndex >= 6 && chanelConfig === 1 || (/vivaldi/i).test(userAgent))
                || !audioCodec && chanelConfig === 1
            ) {
                objectType = 2;
                config = new Array(2);
            }
            extensionSampleingIndex = sampleingIndex;
        }
    }
    /* refer to http://wiki.multimedia.cx/index.php?title=MPEG-4_Audio#Audio_Specific_Config
      ISO 14496-3 (AAC).pdf - Table 1.13  Syntax of AudioSpecificConfig()
    Audio Profile / Audio Object Type
    0: Null
    1: AAC Main
    2: AAC LC (Low Complexity)
    3: AAC SSR (Scalable Sample Rate)
    4: AAC LTP (Long Term Prediction)
    5: SBR (Spectral Band Replication)
    6: AAC Scalable
   sampling freq
    0: 96000 Hz
    1: 88200 Hz
    2: 64000 Hz
    3: 48000 Hz
    4: 44100 Hz
    5: 32000 Hz
    6: 24000 Hz
    7: 22050 Hz
    8: 16000 Hz
    9: 12000 Hz
    10: 11025 Hz
    11: 8000 Hz
    12: 7350 Hz
    13: Reserved
    14: Reserved
    15: frequency is written explictly
    Channel Configurations
    These are the channel configurations:
    0: Defined in AOT Specifc Config
    1: 1 channel: front-center
    2: 2 channels: front-left, front-right
  */
    // audioObjectType = profile => profile, the MPEG-4 Audio Object Type minus 1
    config[0] = objectType << 3;
    // samplingFrequencyIndex
    config[0] |= (sampleingIndex & 0x0e) >> 1;
    config[1] |= (sampleingIndex & 0x01) << 7;
    // channelConfiguration
    config[1] |= chanelConfig << 3;
    if (objectType === 5) {
        // extensionSampleingIndex
        config[1] |= (extensionSampleingIndex & 0x0e) >> 1;
        config[2] = (extensionSampleingIndex & 0x01) << 7;
        // objectType (force to 2, chrome is checking that object type is less than 5 ???
        //    https://chromium.googlesource.com/chromium/src.git/+/master/media/formats/mp4/aac.cc
        config[2] |= 2 << 2;
        config[3] = 0;
    }
    return {
        config: config,
        samplerate: sampleingRates[sampleingIndex],
        channelCount: chanelConfig,
        codec: 'mp4a.40.' + objectType,
        manifestCodec: manifestCodec
    };
}
