import {EventEmitter} from 'events';
import {Log} from '../utils/log';
import {Matrix} from '../utils/matrix';
import AbrLevel from './abr-level';
import {AbrManifest} from './abr-manifest';

type AdaptiveConfig = {
    beacon: boolean;
    predictStep: number;
    minBuffer: number; // buffer
    bufferOffset: number; // buffer
    duration: number; // 
    smoothRatioToTarget: number;
    speedSmoothRatio: number;
    switchPenalty: number;
    slidingWindowSize: number;
    kalmanR: number;
    kalmanQ: number;
    initBitrateLevel: number;
    speedPredictNum: number;
};

const tag = 'algorithm-flv';

const CONFIG: AdaptiveConfig = {
    beacon: false, // 
    predictStep: 5,
    minBuffer: 0.2, // buffer
    bufferOffset: -1,
    duration: 2, // 
    smoothRatioToTarget: 0.25,
    speedSmoothRatio: 0.5,
    switchPenalty: 1.5 * 1e-6,
    slidingWindowSize: 3,
    kalmanR: 0.2 ** 2,
    kalmanQ: 4e-4,
    initBitrateLevel: 3, // 
    speedPredictNum: 3, // 
};

/**
 * HLS
 */
class AbrAlgorithm extends EventEmitter {
    private MATRIX_E!: Matrix;
    private MATRIX_G!: Matrix;

    private _conf!: AdaptiveConfig;

    private _xPrev: number = 0;
    private _PPrev: number = 0;
    private _pastBuffer!: number[];
    private _pastThroughput!: number[];
    private _levels!: AbrLevel[];
    private _availableList?: number[];
    private _current: number = 0; // index
    private _next: number = 0; // index

    constructor() {
        super();
    }

    /**
     * 
     * @param manifest 
     * @param config 
     */
    public init(manifest: AbrManifest, config?: any): void {
        // TODO: config
        this._processConfig(config);

        Log.i(tag, 'init', manifest, config, this._conf);
        this._levels = manifest.levels.slice(0);
        // TODO: defaultconfig.initBitrateLevel
        this._current = manifest.default;

        // initialize the parameters
        this._next = 0;
        this._pastBuffer = [0, 0];
        this._pastThroughput = new Array(this._conf.slidingWindowSize);
        this._pastThroughput[this._conf.slidingWindowSize - 1] = 0;
        // initialize the parameters of kalman filter
        this._xPrev = 0;
        this._PPrev = 0;
        // matrix initialization
        this.MATRIX_E = Matrix.zeros(
            this._conf.predictStep,
            this._conf.predictStep
        );
        this.MATRIX_G = Matrix.zeros(this._conf.predictStep, 2);

        for (let i = 0; i < this._conf.predictStep; i++) {
            for (let j = 0; j < i + 1; j++) {
                this.MATRIX_E.set(i, j, i - j + 1);
            }
        }
        for (let i = 0; i < this._conf.predictStep; i++) {
            this.MATRIX_G.set(i, 0, i + 2);
            this.MATRIX_G.set(i, 1, -i - 1);
        }
    }

    /**
     * 
     * @param list index
     */
    public setAvailableBitrates(list: number[]): void {
        if (list.length) {
            this._availableList = list;
        }
    }

    /**
     * 
     * @returns {number} index
     */
    public get nextLevel(): number {
        return this._next;
    }

    /**
     * 
     * @param buffered buffer
     * @param size 
     * @param time 
     */
    public onGOP(buffered: number, size: number, time: number): void {
        // Byte/s -> kbps: {x} * 1000 * 8 / 1024;
        let speed = (size / time) * 8 / 1024;
        this._next = this._nextRateIndex(
            this._recentSpeed(speed),
            buffered
        );
    }

    /**
     * 
     * @param index index
     */
    public onLevelLoad(index: number): void {
        this._current = Math.max(0, index);
    }

    /**
     * 
     * @param config 
     */
    private _processConfig(config?: any): void {
        const c = Object.assign({}, CONFIG);
        this._conf = Object.assign(c, config);
    }

    private _quantization(bitrate: number, speed: number): number {
        let index = this._current;
        for (let i = this._levels.length - 1; i >= 0; i--) {
            if (bitrate >= this._levels[i].bitrate) {
                index = i;
                break;
            }
        }
        if (index > this._current && speed < this._levels[index].bitrate) {
            index -= 1;
        }
        return index;
    }

    /**
     * 
     * @param value  kpbs
     * @param buffered buffer ms
     */
    private _nextRateIndex(value: number, buffered: number): number {
        this._pastThroughput.shift();
        this._pastThroughput.push(value);

        let index = this._nextRateBySpeedAndBuffered(value, buffered);
        if (index >= this._current) {
            this._pastBuffer.shift();
            this._pastBuffer.push(buffered);
        } else {
            this._pastBuffer = [buffered, buffered];
        }

        if (
            this._availableList &&
            this._availableList.length &&
            this._availableList.indexOf(index) === -1
        ) {
            for (let i = this._availableList.length - 1; i >= 0; i--) {
                if (this._availableList[i] <= index) {
                    index = this._availableList[i];
                    break;
                }
            }
            index = Math.max(index, this._availableList[0]);
        }
        return index;
    }

    /**
     * 
     */
    private _resetAlgorithm() {
        this._pastBuffer = [0, 0];
        this._pastThroughput = new Array(this._conf.slidingWindowSize);
    }

    /**
     * buffer
     */
    private _recentBuffer(): number {
        return this._pastBuffer.reduce((a, b) => a + b) / this._pastBuffer.length;
    }

    /**
     * 
     */
    private _recentSpeed(value: number): number {
        const speed = this._pastThroughput[this._pastThroughput.length - 1];
        if (speed > 0) {
            return speed * this._conf.speedSmoothRatio + value * (1 - this._conf.speedSmoothRatio);
        }
        return value;
    }

    /**
     * bufferTarget Buffer
     */
    private _adjustedTargetBuffer(): number {
        const targetBuffer = Math.max(this._conf.minBuffer, this._recentBuffer() + this._conf.bufferOffset);
        Log.v(tag, `targetBuffer:${targetBuffer}`);
        return targetBuffer;
    }

    /**
     * 
     * @param switchPenaltyArray 
     * @param estimatedThroughput 
     * @param Bk buffer
     * @param br buffer
     */
    private _rateChangeArray(switchPenaltyArray: Matrix, estimatedThroughput: number[], Bk: Matrix, br: number[]): any {
        const targetBufferArray = new Matrix(br);
        this._printFirstColumn(targetBufferArray, 'targetBufferArray');

        const matrixG = this.MATRIX_G;
        const predictedBufferArray = Matrix.multiply(matrixG, Bk);
        this._printFirstColumn(predictedBufferArray, 'predictedBufferArray');

        const matrixE = this.MATRIX_E;
        const matrixF = matrixE.multiply(this._diag(estimatedThroughput));
        return Matrix.multiply(
            Matrix.multiply(
                Matrix.add(
                    Matrix.multiply(matrixF.transpose(), matrixF),
                    switchPenaltyArray
                ).inverse(),
                matrixF.transpose()
            ),
            Matrix.subtract(targetBufferArray, predictedBufferArray)
        );
    }

    /**
     * buffer
     * @param speed  kbps
     * @param buffered buffer ms
     */
    private _nextRateBySpeedAndBuffered(
        speed: number,
        buffered: number
    ): number {
        const switchPenaltyArray = Matrix.zeros(
            this._conf.predictStep,
            this._conf.predictStep
        );
        for (let i = 0; i < this._conf.predictStep; i++) {
            switchPenaltyArray.set(
                i,
                i,
                this._conf.switchPenalty * (this._conf.predictStep - i)
            );
        }

        const futureThroughput = this._multistepPred(
            this._pastThroughput,
            this._conf.predictStep
        );
        // const result = this._multistepKalmanfilter(
        //     this._xPrev,
        //     this._pastThroughput[this._pastThroughput.length - 1],
        //     this._PPrev,
        //     this._conf.predictStep
        // );
        // const futureThroughput = result.futureThroughput;
        // this._xPrev = result.xRet;
        // this._PPrev = result.PRet;
        Log.v(tag, `futureThroughput:${futureThroughput}`);
        const estimatedThroughput = futureThroughput.map(item => {
            return -(this._conf.duration / item);
        });

        const Bk = new Matrix([[buffered], [this._recentBuffer()]]);

        const br: any[] = [];
        br[0] = [buffered];
        const target = this._adjustedTargetBuffer();
        for (let i = 0; i < this._conf.predictStep; i++) {
            br[i + 1] = [
                this._conf.smoothRatioToTarget * br[i] +
                (1 - this._conf.smoothRatioToTarget) * target
            ];
        }
        const rateChangeArray = this._rateChangeArray(switchPenaltyArray, estimatedThroughput, Bk, br.slice(1));
        Log.v(
            tag,
            `algorithm input speed:${speed} buffered:${buffered}`,
            `output result:${rateChangeArray.get(0, 0)}`
        );
        return this._quantization(
            (this._levels[this._current].bitrate + rateChangeArray.get(0, 0)),
            speed
        );
    }

    private _printFirstColumn(matrix: Matrix, name: string) {
        let out = [];
        for (let i = 0; i < matrix.shape[0]; ++i) {
            out[i] = +matrix.get(i, 0).toFixed(2);
        }
        Log.v(tag, name, JSON.stringify(out));
    }

    private _printFirstRow(matrix: Matrix, name: string) {
        let out = [];
        for (let i = 0; i < matrix.shape[0]; ++i) {
            out[i] = +matrix.get(0, i).toFixed(2);
        }
        Log.v(tag, name, JSON.stringify(out));
    }

    private _diag(arr: any[]): Matrix {
        const dist = [],
            len = arr.length;
        for (let i = 0; i < len; i++) {
            dist[i] = new Array(len);
            for (let j = 0; j < len; j++) {
                dist[i][j] = 0;
            }
            dist[i][i] = arr[i];
        }
        return new Matrix(dist, { shape: [len, len] });
    }

    private _multistepPred(
        pastThroughput: number[],
        predictStep: number
    ): number[] {
        const futureThroughput = new Array(predictStep);
        const pastThroughputClone = pastThroughput.slice(0);
        for (let i = 0; i < predictStep; i++) {
            let bandwidthSum = 0,
                nonzeroCnt = 0;
            for (let j = 0; j < pastThroughput.length; j++) {
                if (pastThroughputClone[j]) {
                    bandwidthSum += 1.0 / pastThroughputClone[j];
                    nonzeroCnt += 1;
                }
            }
            if (nonzeroCnt === 0) {
                continue;
            } else {
                futureThroughput[i] = 1.0 / (bandwidthSum / nonzeroCnt);
            }
            pastThroughputClone.shift();
            pastThroughputClone.push(futureThroughput[i]);
        }
        return futureThroughput;
    }

    private _multistepKalmanfilter(
        xLast: number,
        z: number,
        PLast: number,
        predictStep: number
    ) {
        const R = this._conf.kalmanR,
            Q = this._conf.kalmanQ;
        const futureThroughput = new Array(this._conf.predictStep);
        let xPrev = xLast,
            PPrev = PLast,
            xRet = 0,
            PRet = 0;
        for (let i = 0; i < predictStep; i++) {
            const xMinus = xPrev,
                PMinus = PPrev + Q,
                K = PMinus / (PMinus + R),
                predX = xMinus + K * (z - xMinus),
                P = (1 - K) * PMinus;
            if (i === 0) {
                xRet = predX;
                PRet = P;
            }
            futureThroughput[i] = predX;
            xPrev = predX;
            PPrev = P;
        }
        return { futureThroughput, xRet, PRet };
    }
}

export default AbrAlgorithm;
