class AVC {
    static getFlvVideoTag(timestamp: number, config: boolean = false): FlvTag {
        const tag = new FlvTag();
        tag.tagType = FlvTagType.VIDEO;
        if (config) {
            tag.body = configBody;
        } else {
            tag.body = frameI;
        }
        tag.dataSize = tag.body.byteLength;
        tag.timestamp = Math.round(timestamp);
        tag.frameType = 1;
        tag.codecId = 7;
        tag.fill = true;
        return tag;
    }
}

export default AVC;
