import Observer from '../../core/observer';
import LasEvents from '../../core/events';
import FlvDemuxerInline, { FlvExtraData } from './flv-demuxer-inline';
import { Log } from '../../utils/log';
import { FLVTransConfig } from '../../types/core';

export default function (self: any) {
    let flv: FlvDemuxerInline;

    const observer = new Observer();
    const forwardMessage = function (ev: string, data: any) {
        self.postMessage({ event: ev, data: data });
    };

    observer.on(LasEvents.PARSING_INIT_SEGMENT, forwardMessage);
    observer.on(LasEvents.ERROR, forwardMessage);
    observer.on(LasEvents.SCRIPT_PARSED, forwardMessage);
    observer.on(LasEvents.LOAD_END, forwardMessage);
    observer.on(LasEvents.PARSING_DATA, function (ev, data) {
        const message: any = { event: ev, data: data };
        message.payload = data.payload;
        delete data.payload;
        self.postMessage(message, [message.payload.buffer]);
    });

    function init(observer: Observer, config: FLVTransConfig, data: FlvExtraData, vendor: string) {
        flv = new FlvDemuxerInline(observer, config, data, vendor);
        flv.init();
    }

    function destroy() {
        if (flv) {
            flv.destroy();
        }
        if (observer) {
            observer.removeAllListeners();
        }
    }

    self.addEventListener('message', function (e: any) {
        const d = e.data;
        switch (d.cmd) {
            case 'init':
                Log.level(d.config.debug);
                init(observer, d.config, d.data, d.vendor);
                break;
            case 'destroy':
                destroy();
                break;
            case 'append':
                flv.append(d.tags, d.timeOffset, d.discontinuity, d.contiguous, d.accurateTimeOffset);
                break;
            case 'flvHead':
                flv.flvHead(d.hasAudio, d.hasVideo);
                break;
            case 'flush':
                flv.flush();
                break;
            case 'setExtra':
                flv.setExtra(d.data);
                break;
            case 'end':
                flv.end();
                break;
        }
    });
}
