export default class AbrLevel {
    public url: string;
    public bitrate: number = 0;
    public maxBitrate: number = 0;
    public avgBitrate: number = 0;
    public qualityType: string = '';
    public qualityLabel: string = '';
    public id: number = 0;
    public codec: string = '';
    public hidden: boolean = false;
    public enableAdaptive: boolean = true;
    public defaultSelect: boolean = false;

    constructor(url: string) {
        this.url = url;
    }
}