export type TypedArray
= | Int8Array
    | Uint8Array
    | Int16Array
    | Uint16Array
    | Int32Array
    | Uint32Array
    | Uint8ClampedArray
    | Float32Array
    | Float64Array;

export type TypedArrayConstructor
= | Int8ArrayConstructor
    | Uint8ArrayConstructor
    | Int16ArrayConstructor
    | Uint16ArrayConstructor
    | Int32ArrayConstructor
    | Uint32ArrayConstructor
    | Uint8ClampedArrayConstructor
    | Float32ArrayConstructor
    | Float64ArrayConstructor;

const type: (input: TypedArray) => TypedArrayConstructor = (input: TypedArray): TypedArrayConstructor => {
    switch (input.constructor.name) {
        case 'Int8Array':
            return Int8Array;
        case 'Uint8Array':
            return Uint8Array;
        case 'Int16Array':
            return Int16Array;
        case 'Uint16Array':
            return Uint16Array;
        case 'Int32Array':
            return Int32Array;
        case 'Uint32Array':
            return Uint32Array;
        case 'Uint8ClampedArray':
            return Uint8ClampedArray;
        case 'Float64Array':
            return Float64Array;
        default:
            return Float32Array;
    }
};

const flatten: (input: any[]) => number[] = (input: any[]): number[] =>
    input.reduce((acc: any[], next: any) => acc.concat(Array.isArray(next) ? flatten(next) : next), []);

const shape: (input: any) => number[] = (input: any): number[] =>
    Array.isArray(input) ? [input.length].concat(shape(input[0])) : [];

const isTypedArray: (input: any) => boolean = (input: any): boolean =>
    !!(input && input.buffer instanceof ArrayBuffer && input.BYTES_PER_ELEMENT);

export class Matrix {
    /**
     * Static method. Adds two matrices `a` and `b` together.
     */
    public static add(a: Matrix, b: Matrix): Matrix {
        return a.copy().add(b);
    }

    /**
     * Static method. Subtracts the matrix `b` from matrix `a`.
     */
    public static subtract(a: Matrix, b: Matrix): Matrix {
        return a.copy().subtract(b);
    }

    /**
     * Static method. Augments two matrices `a` and `b` of matching dimensions
     * (appends `b` to `a`).
     */
    public static augment(a: Matrix, b: Matrix): Matrix {
        return a.copy().augment(b);
    }

    /**
     * Static method. Multiplies two matrices `a` and `b` of matching dimensions.
     */
    public static multiply(a: Matrix, b: Matrix): Matrix {
        return a.multiply(b);
    }

    /**
     * Static method. Creates an `r x c` matrix containing zeros (`0`), takes an
     * optional `type` argument which should be an instance of `TypedArray`.
     */
    public static zeros(r: number, c: number, type: TypedArrayConstructor = Float32Array): Matrix {
        return Matrix.fill(r, c, 0, type);
    }

    /**
     * Static method. Creates a `r x c` matrix containing optional 'value' (default 0), takes
     * an optional `type` argument which should be an instance of `TypedArray`.
     */
    public static fill(
        r: number,
        c: number,
        value: number | ((index: number) => number) = 0,
        type: TypedArrayConstructor = Float32Array
    ): Matrix {
        if (r <= 0 || c <= 0) {
            throw new Error('invalid size');
        }

        const size: number = r * c;
        const data: TypedArray = new type(size);

        return new Matrix(data, { shape: [r, c] }).fill(value);
    }

    public data: TypedArray = new Float32Array(0);
    public length: number = 0;
    public shape: number[] = [0];
    public type: TypedArrayConstructor = Float32Array;

    /**
     * Static method. Creates an identity matrix of `size`, takes an optional `type` argument
     * which should be an instance of `TypedArray`.
     */
    public static identity(size: number, type: TypedArrayConstructor = Float32Array): Matrix {
        return Matrix.fill(size, size, (i: number) => i % size === Math.floor(i / size) ? 1 : 0, type);
    }

    constructor(data?: any, options?: any) {
        if (typeof data === 'number' && typeof options === 'number') {
            this._init(new Float32Array(data * options), { shape: [data, options] });
        } else {
            this._init(data, options);
        }
    }

    private _init(
        data?: any,
        options?: {
        shape: number[];
        }
    ) {
        if (isTypedArray(data)) {
            this.data = data as TypedArray;
            this.shape = typeof options === 'object' ? options.shape : [this.data.length];
            this.length = this.data.length;
            this.type = type(data);
        } else if (data instanceof Array) {
            this.data = new Float32Array(flatten(data));
            this.shape = shape(data);
            this.length = this.data.length;
        } else if (data instanceof Matrix) {
            return data.copy();
        }
    }

    public multiply(matrix: Matrix): Matrix {
        const [r1, c1] = this.shape;
        const [r2, c2] = matrix.shape;

        if (c1 !== r2) {
            throw new Error('sizes do not match');
        }

        const { data: d1 } = this;
        const { data: d2 } = matrix;
        const data: TypedArray = new this.type(r1 * c2);

        let i: number;
        let j: number;
        let k: number;
        let sum: number;
        for (i = 0; i < r1; i += 1) {
            for (j = 0; j < c2; j += 1) {
                sum = 0;
                for (k = 0; k < c1; k += 1) {
                    sum += d1[i * c1 + k] * d2[j + k * c2];
                }

                data[i * c2 + j] = sum;
            }
        }

        return new Matrix(data, { shape: [r1, c2] });
    }

    /**
     * Adds `x` multiplied by `alpha` to the current array.
     */
    public add(x: Matrix, alpha: number = 1): this {
        this.equilateral(x);
        this.equidimensional(x);

        const { data: d1, length: l1 } = this;
        const { data: d2 } = x;

        let i: number;
        for (i = 0; i < l1; i += 1) {
            d1[i] += alpha * d2[i];
        }

        return this;
    }

    /**
     * Subtracts `x` to the current array.
     */
    public subtract(x: Matrix): this {
        return this.add(x, -1);
    }

    /**
     * Augments `matrix` with current matrix.
     */
    public augment(matrix: Matrix): Matrix {
        const [r1, c1] = this.shape;
        const [r2, c2] = matrix.shape;

        if (r2 === 0 || c2 === 0) {
            return this;
        }

        if (r1 !== r2) {
            throw new Error('rows do not match');
        }

        const { data: d1 } = this;
        const { data: d2 } = matrix;
        const length: number = c1 + c2;
        const data: TypedArray = new this.type(length * r1);

        let i: number;
        let j: number;
        for (i = 0; i < r1; i += 1) {
            for (j = 0; j < c1; j += 1) {
                data[i * length + j] = d1[i * c1 + j];
            }
        }

        for (i = 0; i < r2; i += 1) {
            for (j = 0; j < c2; j += 1) {
                data[i * length + j + c1] = d2[i * c2 + j];
            }
        }

        this.shape = [r1, length];
        this.length = data.length;
        this.data = data;

        return this;
    }

    /**
     * Fills the array with a scalar value, takes an optional `type` argument
     * which should be an instance of `TypedArray`.
     */
    public fill(value: number | ((index: number) => number) = 0): this {
        const { data, length } = this;

        let i: number;
        for (i = 0; i < length; i += 1) {
            data[i] = value instanceof Function ? value(i) : value;
        }

        return this;
    }

    /**
     * Transposes a matrix (mirror across the diagonal).
     */
    public transpose(): Matrix {
        const [r, c] = this.shape;
        const data: TypedArray = new this.type(c * r);

        let i: number;
        let j: number;
        for (i = 0; i < r; i += 1) {
            for (j = 0; j < c; j += 1) {
                data[j * r + i] = this.data[i * c + j];
            }
        }

        return new Matrix(data, { shape: [c, r] });
    }

    /**
     * Determines the inverse of any invertible square matrix using
     * Gaussian elimination.
     */
    public inverse(): Matrix {
        const [r, c] = this.shape;

        if (r !== c) {
            throw new Error('invalid dimensions');
        }

        const identity: Matrix = Matrix.identity(r);
        const augmented: Matrix = Matrix.augment(this, identity);
        const gauss: Matrix = augmented.gauss();
        const left: Matrix = Matrix.zeros(r, c);
        const right: Matrix = Matrix.zeros(r, c);

        const n: number = gauss.shape[1];

        let i: number;
        let j: number;
        for (i = 0; i < r; i += 1) {
            for (j = 0; j < n; j += 1) {
                if (j < c) {
                    left.set(i, j, gauss.get(i, j));
                } else {
                    right.set(i, j - r, gauss.get(i, j));
                }
            }
        }

        if (!left.equals(Matrix.identity(r))) {
            throw new Error('matrix is not invertible');
        }

        return right;
    }

    /**
     * Performs Gaussian elimination on a matrix.
     */
    public gauss(): Matrix {
        const [r, c] = this.shape;
        const copy: Matrix = this.copy();

        let lead: number = 0;
        let pivot: number;
        let leadValue: number;

        let i: number;
        let j: number;
        let k: number;
        for (i = 0; i < r; i += 1) {
            if (c <= lead) {
                throw new Error('matrix is singular');
            }

            j = i;
            while (copy.data[j * c + lead] === 0) {
                j += 1;
                if (r === j) {
                    j = i;
                    lead += 1;

                    if (c === lead) {
                        throw new Error('matrix is singular');
                    }
                }
            }

            copy.swap(i, j);

            pivot = copy.data[i * c + lead];
            if (pivot !== 0) {
                for (k = 0; k < c; k += 1) {
                    copy.data[i * c + k] = copy.data[i * c + k] / pivot;
                }
            }

            for (j = 0; j < r; j += 1) {
                leadValue = copy.data[j * c + lead];
                if (j !== i) {
                    for (k = 0; k < c; k += 1) {
                        copy.data[j * c + k] = copy.data[j * c + k] - copy.data[i * c + k] * leadValue;
                    }
                }
            }

            lead += 1;
        }

        for (i = 0; i < r; i += 1) {
            pivot = 0;
            for (j = 0; j < c; j += 1) {
                if (pivot === 0) {
                    pivot = copy.data[i * c + j];
                }
            }

            if (pivot === 0) {
                for (k = 0; k < c; k += 1) {
                    copy.data[i * c + k] = copy.data[i * c + k] / pivot;
                }
            }
        }

        return copy;
    }

    /**
     * Checks if current array and `x` are equal.
     */
    public equals(x: Matrix): boolean {
        this.equilateral(x);
        this.equidimensional(x);

        const { data: d1, length: l1 } = this;
        const { data: d2 } = x;

        let i: number;
        for (i = 0; i < l1; i += 1) {
            if (d1[i] !== d2[i]) {
                return false;
            }
        }

        return true;
    }

    /**
     * Check if `i` and `j` is within the bounds for current matrix.
     */
    public check(i: number, j: number): void {
        const [r, c] = this.shape;

        if (isNaN(i) || isNaN(j)) {
            throw new Error('one of the indices is not a number');
        }

        if (i < 0 || j < 0 || i > r - 1 || j > c - 1) {
            throw new Error('index out of bounds');
        }
    }

    /**
     * Sets the element at row `i`, column `j` to value
     */
    public set(i: number, j: number, value: number): Matrix {
        this.check(i, j);
        this.data[i * this.shape[1] + j] = value;

        return this;
    }

    /**
     * Gets the value of the element in row `i`, column `j` of current matrix
     */
    public get(i: number, j: number): number {
        this.check(i, j);

        return this.data[i * this.shape[1] + j];
    }

    /**
     * Swaps two rows `i` and `j` in a matrix
     */
    public swap(i: number, j: number): Matrix {
        const [r, c] = this.shape;
        if (i < 0 || j < 0 || i > r - 1 || j > r - 1) {
            throw new Error('index out of bounds');
        }

        if(this.data.slice) {
            const copy: TypedArray = this.data.slice(i * c, (i + 1) * c);
            this.data.copyWithin(i * c, j * c, (j + 1) * c);
            this.data.set(copy, j * c);
        } else {
            // IE 11  slice copyWithin 
            const D:any = this.data.constructor;
            const copy: TypedArray = new D(this.data.buffer.slice(0));
            this.data.set(copy.subarray(i * c, (i + 1) * c), j * c);
            this.data.set(copy.subarray(j * c, (j + 1) * c), i * c);
        }

        return this;
    }
    /**
     * Makes a copy of the class and underlying data
     */
    public copy(): this {
        const copy: Matrix = Object.assign(Object.create(Object.getPrototypeOf(this)), this) as Matrix;

        copy.data = new this.type(this.data);
        copy.shape = this.shape;
        copy.length = this.length;
        copy.type = this.type;

        return copy as this;
    }

    /**
     * Asserts if current array and `x` have the same shape
     */
    public equidimensional(x: Matrix): void {
        const { shape: s1 } = this;
        const { shape: s2 } = x;

        if (!s1.every((dim: number, i: number) => dim === s2[i])) {
            throw new Error(`shapes ${s1} and ${s2} do not match`);
        }
    }

    /**
     * Asserts if current array and `x` have the same length
     */
    public equilateral(x: Matrix): void {
        const { length: l1 } = this;
        const { length: l2 } = x;

        if (l1 !== l2) {
            throw new Error(`lengths ${l1} and ${l2} do not match`);
        }
    }
}
