/* eslint-disable */
import ExpGolomb from './exp-golomb';

const HEVC_MAX_SPS_COUNT = 16;
const HEVC_MAIN: Record<number, string> = { 1: 'Main', 2: 'Main 10', 3: 'Main Still Picture', 4: 'Rext' };
const HEVC_LEVELS: Record<number, number> = {
    30: 1,
    60: 2,
    63: 2.1,
    90: 3,
    93: 3.1,
    120: 4,
    123: 4.1,
    150: 5,
    153: 5.1,
    156: 5.2,
    180: 6,
    183: 6.1,
    186: 6.2,
    255: 8.5
};

type SPSInfo = {
    profile_string: string; // baseline, high, high10, ...
    level_string: string; // 3, 3.1, 4, 4.1, 5, 5.1, ...
    bit_depth: number; // 8bit, 10bit, ...
    chroma_format: number; // 4:2:0, 4:2:2, ...
    chroma_format_string: string;
    frame_rate: {
        fixed: boolean;
        fps: number;
        fps_den: number;
        fps_num: number;
    };
    sar_ratio: {
        width: number;
        height: number;
    };
    codec_size: {
        width: number;
        height: number;
    };
    present_size: {
        width: number;
        height: number;
    };
};

type HEVCSPSInfo = {
    chroma_format_string: string;
    profile_string: string;
    level_string: string;
    width: number;
    height: number;
};

class SPSParser {
    static _ebsp2rbsp(uint8array: Uint8Array) {
        const src = uint8array;
        const src_length = src.byteLength;
        const dst = new Uint8Array(src_length);
        let dst_idx = 0;

        for (let i = 0; i < src_length; i++) {
            if (i >= 2) {
                // Unescape: Skip 0x03 after 00 00
                if (src[i] === 0x03 && src[i - 1] === 0x00 && src[i - 2] === 0x00) {
                    continue;
                }
            }
            dst[dst_idx] = src[i];
            dst_idx++;
        }

        return new Uint8Array(dst.buffer, 0, dst_idx);
    }

    static parseHEVCSPS(uint8array: Uint8Array): HEVCSPSInfo | null {
        const rbsp = SPSParser._ebsp2rbsp(uint8array);
        let gb: ExpGolomb | null = new ExpGolomb(rbsp);
        const ptl: any = { general_ptl: {}, sub_layer_ptl: {} };
        gb.readBits(16);
        gb.readBits(4); // sps_video_parameter_set_id
        let max_sub_layers = gb.readBits(3) + 1; // sps_max_sub_layers_minus1
        gb.readBits(1); // temporalIdNested

        if (!SPSParser._parsePTL(gb, ptl, max_sub_layers)) {
            return null;
        }

        const sps_id = gb.readUEG();
        if (sps_id >= HEVC_MAX_SPS_COUNT) {
            return null;
        }
        let chroma_format_idc = gb.readUEG();

        if (chroma_format_idc == 3) {
            const separate_colour_plane_flag = gb.readBits(1);
            if (separate_colour_plane_flag) {
                chroma_format_idc = 0;
            }
        }

        const width = gb.readUEG();
        const height = gb.readUEG();
        gb.destroy();
        gb = null;
        // let conformance_window_flag = gb.readBits(1);
        return {
            chroma_format_string: chroma_format_idc === 3 ? '4:4:4' : '4:2:0',
            profile_string: ptl.general_ptl.profile_string,
            level_string: ptl.general_ptl.level_string,
            width: width,
            height: height
        };
        // hvcc_parse_ptl end
    }

    static _parsePTL(gb: ExpGolomb, ptl: any, max_num_sub_layers: number) {
        if (
            !this._decodeProfileTierLevel(gb, ptl.general_ptl) ||
            gb.getBitsLeft() < 8 + 8 * 2 * (max_num_sub_layers - 1 > 0 ? 1 : 0)
        ) {
            return false;
        }

        ptl.general_ptl.level_idc = gb.readBits(8);
        ptl.general_ptl.level_string = this.getHEVCLevelString(ptl.general_ptl.level_idc);
        ptl.sub_layer_profile_present_flag = [];
        ptl.sub_layer_level_present_flag = [];
        for (let i = 0; i < max_num_sub_layers - 1; i++) {
            ptl.sub_layer_profile_present_flag[i] = gb.readBits(1);
            ptl.sub_layer_level_present_flag[i] = gb.readBits(1);
        }

        if (max_num_sub_layers - 1 > 0) {
            for (let i = max_num_sub_layers - 1; i < 8; i++) {
                gb.readBits(2); // reserved_zero_2bits[i]
            }
        }
        for (let i = 0; i < max_num_sub_layers - 1; i++) {
            if (ptl.sub_layer_profile_present_flag[i] && this._decodeProfileTierLevel(gb, ptl.sub_layer_ptl)) {
                return false;
            }
            if (ptl.sub_layer_level_present_flag[i]) {
                if (gb.getBitsLeft() < 8) {
                    return false;
                } else ptl.sub_layer_ptl[i].level_idc = gb.readBits(8);
            }
        }
        return true;
    }

    static _decodeProfileTierLevel(gb: ExpGolomb, ptl: any) {
        if (gb.getBitsLeft() < 2 + 1 + 5 + 32 + 4 + 16 + 16 + 12) {
            return false;
        }

        ptl.profile_space = gb.readBits(2);
        ptl.tier_flag = gb.readBits(1);
        ptl.profile_idc = gb.readBits(5);
        ptl.profile_compatibility_flag = [];

        for (let i = 0; i < 32; i++) {
            ptl.profile_compatibility_flag[i] = gb.readBits(1);
            if (ptl.profile_idc == 0 && i > 0 && ptl.profile_compatibility_flag[i]) ptl.profile_idc = i;
        }
        ptl.progressive_source_flag = gb.readBits(1);
        ptl.interlaced_source_flag = gb.readBits(1);
        ptl.non_packed_constraint_flag = gb.readBits(1);
        ptl.frame_only_constraint_flag = gb.readBits(1);

        gb.readBits(16);
        gb.readBits(16);
        gb.readBits(12);
        ptl.profile_string = this.getHEVCProfileString(ptl.profile_idc);
        return true;
    }

    static parseSPS(uint8array: Uint8Array): SPSInfo {
        const rbsp = SPSParser._ebsp2rbsp(uint8array);
        let gb: ExpGolomb | null = new ExpGolomb(rbsp);

        gb.readByte();
        const profile_idc = gb.readByte(); // profile_idc
        gb.readByte(); // constraint_set_flags[5] + reserved_zero[3]
        const level_idc = gb.readByte(); // level_idc
        gb.readUEG(); // seq_parameter_set_id

        const profile_string = SPSParser.getProfileString(profile_idc);
        const level_string = SPSParser.getLevelString(level_idc);
        let chroma_format_idc = 1;
        let chroma_format = 420;
        const chroma_format_table = [0, 420, 422, 444];
        let bit_depth = 8;

        if (
            profile_idc === 100 ||
            profile_idc === 110 ||
            profile_idc === 122 ||
            profile_idc === 244 ||
            profile_idc === 44 ||
            profile_idc === 83 ||
            profile_idc === 86 ||
            profile_idc === 118 ||
            profile_idc === 128 ||
            profile_idc === 138 ||
            profile_idc === 144
        ) {
            chroma_format_idc = gb.readUEG();
            if (chroma_format_idc === 3) {
                gb.readBits(1); // separate_colour_plane_flag
            }
            if (chroma_format_idc <= 3) {
                chroma_format = chroma_format_table[chroma_format_idc];
            }

            bit_depth = gb.readUEG() + 8; // bit_depth_luma_minus8
            gb.readUEG(); // bit_depth_chroma_minus8
            gb.readBits(1); // qpprime_y_zero_transform_bypass_flag
            if (gb.readBool()) {
                // seq_scaling_matrix_present_flag
                const scaling_list_count = chroma_format_idc !== 3 ? 8 : 12;
                for (let i = 0; i < scaling_list_count; i++) {
                    if (gb.readBool()) {
                        // seq_scaling_list_present_flag
                        if (i < 6) {
                            SPSParser._skipScalingList(gb, 16);
                        } else {
                            SPSParser._skipScalingList(gb, 64);
                        }
                    }
                }
            }
        }
        gb.readUEG(); // log2_max_frame_num_minus4
        const pic_order_cnt_type = gb.readUEG();
        if (pic_order_cnt_type === 0) {
            gb.readUEG(); // log2_max_pic_order_cnt_lsb_minus_4
        } else if (pic_order_cnt_type === 1) {
            gb.readBits(1); // delta_pic_order_always_zero_flag
            gb.readSEG(); // offset_for_non_ref_pic
            gb.readSEG(); // offset_for_top_to_bottom_field
            const num_ref_frames_in_pic_order_cnt_cycle = gb.readUEG();
            for (let i = 0; i < num_ref_frames_in_pic_order_cnt_cycle; i++) {
                gb.readSEG(); // offset_for_ref_frame
            }
        }
        gb.readUEG(); // max_num_ref_frames
        gb.readBits(1); // gaps_in_frame_num_value_allowed_flag

        const pic_width_in_mbs_minus1 = gb.readUEG();
        const pic_height_in_map_units_minus1 = gb.readUEG();

        const frame_mbs_only_flag = gb.readBits(1);
        if (frame_mbs_only_flag === 0) {
            gb.readBits(1); // mb_adaptive_frame_field_flag
        }
        gb.readBits(1); // direct_8x8_inference_flag

        let frame_crop_left_offset = 0;
        let frame_crop_right_offset = 0;
        let frame_crop_top_offset = 0;
        let frame_crop_bottom_offset = 0;

        const frame_cropping_flag = gb.readBool();
        if (frame_cropping_flag) {
            frame_crop_left_offset = gb.readUEG();
            frame_crop_right_offset = gb.readUEG();
            frame_crop_top_offset = gb.readUEG();
            frame_crop_bottom_offset = gb.readUEG();
        }

        let sar_width = 1,
            sar_height = 1;
        let fps = 0,
            fps_fixed = true,
            fps_num = 0,
            fps_den = 0;

        const vui_parameters_present_flag = gb.readBool();
        if (vui_parameters_present_flag) {
            if (gb.readBool()) {
                // aspect_ratio_info_present_flag
                const aspect_ratio_idc = gb.readByte();
                const sar_w_table = [1, 12, 10, 16, 40, 24, 20, 32, 80, 18, 15, 64, 160, 4, 3, 2];
                const sar_h_table = [1, 11, 11, 11, 33, 11, 11, 11, 33, 11, 11, 33, 99, 3, 2, 1];

                if (aspect_ratio_idc > 0 && aspect_ratio_idc < 16) {
                    sar_width = sar_w_table[aspect_ratio_idc - 1];
                    sar_height = sar_h_table[aspect_ratio_idc - 1];
                } else if (aspect_ratio_idc === 255) {
                    sar_width = (gb.readByte() << 8) | gb.readByte();
                    sar_height = (gb.readByte() << 8) | gb.readByte();
                }
            }

            if (gb.readBool()) {
                // overscan_info_present_flag
                gb.readBool(); // overscan_appropriate_flag
            }
            if (gb.readBool()) {
                // video_signal_type_present_flag
                gb.readBits(4); // video_format & video_full_range_flag
                if (gb.readBool()) {
                    // colour_description_present_flag
                    gb.readBits(24); // colour_primaries & transfer_characteristics & matrix_coefficients
                }
            }
            if (gb.readBool()) {
                // chroma_loc_info_present_flag
                gb.readUEG(); // chroma_sample_loc_type_top_field
                gb.readUEG(); // chroma_sample_loc_type_bottom_field
            }
            if (gb.readBool()) {
                // timing_info_present_flag
                const num_units_in_tick = gb.readBits(32);
                const time_scale = gb.readBits(32);
                fps_fixed = gb.readBool(); // fixed_frame_rate_flag

                fps_num = time_scale;
                fps_den = num_units_in_tick * 2;
                fps = fps_num / fps_den;
            }
        }

        let sarScale = 1;
        if (sar_width !== 1 || sar_height !== 1) {
            sarScale = sar_width / sar_height;
        }

        let crop_unit_x = 0,
            crop_unit_y = 0;
        if (chroma_format_idc === 0) {
            crop_unit_x = 1;
            crop_unit_y = 2 - frame_mbs_only_flag;
        } else {
            const sub_wc = chroma_format_idc === 3 ? 1 : 2;
            const sub_hc = chroma_format_idc === 1 ? 2 : 1;
            crop_unit_x = sub_wc;
            crop_unit_y = sub_hc * (2 - frame_mbs_only_flag);
        }

        let codec_width = (pic_width_in_mbs_minus1 + 1) * 16;
        let codec_height = (2 - frame_mbs_only_flag) * ((pic_height_in_map_units_minus1 + 1) * 16);

        codec_width -= (frame_crop_left_offset + frame_crop_right_offset) * crop_unit_x;
        codec_height -= (frame_crop_top_offset + frame_crop_bottom_offset) * crop_unit_y;

        const present_width = Math.ceil(codec_width * sarScale);

        gb.destroy();
        gb = null;

        return {
            profile_string, // baseline, high, high10, ...
            level_string, // 3, 3.1, 4, 4.1, 5, 5.1, ...
            bit_depth, // 8bit, 10bit, ...
            chroma_format, // 4:2:0, 4:2:2, ...
            chroma_format_string: SPSParser.getChromaFormatString(chroma_format),

            frame_rate: {
                fixed: fps_fixed,
                fps,
                fps_den,
                fps_num
            },

            sar_ratio: {
                width: sar_width,
                height: sar_height
            },

            codec_size: {
                width: codec_width,
                height: codec_height
            },

            present_size: {
                width: present_width,
                height: codec_height
            }
        };
    }

    static _skipScalingList(gb: ExpGolomb, count: number) {
        let last_scale = 8,
            next_scale = 8;
        let delta_scale = 0;
        for (let i = 0; i < count; i++) {
            if (next_scale !== 0) {
                delta_scale = gb.readSEG();
                next_scale = (last_scale + delta_scale + 256) % 256;
            }
            last_scale = next_scale === 0 ? last_scale : next_scale;
        }
    }

    static getProfileString(profile_idc: number) {
        switch (profile_idc) {
            case 66:
                return 'Baseline';
            case 77:
                return 'Main';
            case 88:
                return 'Extended';
            case 100:
                return 'High';
            case 110:
                return 'High10';
            case 122:
                return 'High422';
            case 244:
                return 'High444';
            default:
                return 'Unknown';
        }
    }

    static getLevelString(level_idc: number) {
        return (level_idc / 10).toFixed(1);
    }

    static getChromaFormatString(chroma: number) {
        switch (chroma) {
            case 420:
                return '4:2:0';
            case 422:
                return '4:2:2';
            case 444:
                return '4:4:4';
            default:
                return 'Unknown';
        }
    }

    static getHEVCProfileString(profile_idc: number) {
        return HEVC_MAIN[profile_idc];
    }

    static getHEVCLevelString(level_idc: number) {
        return HEVC_LEVELS[level_idc];
    }
}

export default SPSParser;
