export enum TrackType {
    video = 'video',
    audio = 'audio'
}

export enum RemuxerTrackIdConfig {
    video = 1,
    audio
}

export interface Track {
    id: number;
    type: TrackType;
    codec: string;
    manifestCodec?: string;
    container: string;
    timescale: number;
    duration: number;
    samples: any[];
    inputTimeScale: number;
    sequenceNumber: number;
    pid: number;
}
export interface ATrack extends Track {
    config: number[];
    isAAC: boolean;
    samplerate: number;
    channelCount: number;
}
export interface VTrack extends Track {
    width: number;
    height: number;
    codecWidth: number;
    codecHeight: number;
    sps: Uint8Array[];
    pps: Uint8Array[];
    pixelRatio: number[];
    profile: string;
    level: string;
    chromaFormat: string;
    fps: number;
    dropped: number;
    refSampleDuration: number;
}

export interface Sample {
    key: boolean;
    pts: number;
    dts: number;
    units: [];
    debug: string;
}

export interface TypeSupported {
    mp4: boolean;
    mpeg: boolean;
    mp3: boolean;
}

export interface IRemuxer {
    readonly ISGenerated: boolean;
    destroy(): void;
    setExtra(data: any): void;
    resetTimeStamp(): void;
    resetInitSegment(): void;
    getLastPTS(): any;
    flush(): any;
    resetStash(): void;
    remux(
        audioTrack: ATrack,
        videoTrack: VTrack,
        timeOffset: number,
        contiguous: boolean,
        accurateTimeOffset: boolean,
        isFlush: boolean
    ): void;
}
