import { ErrorDetails, ErrorTypes } from '../../core/errors';
import LasEvents from '../../core/events';
import Observer from '../../core/observer';
import { FLVTransConfig } from '../../types/core';
import { ATrack, IRemuxer, TrackType, VTrack } from '../../types/remux';
import { Log } from '../../utils/log';
import * as ASC from '../asc';
import SPSParser from '../sps-parser';
import AMF from './amf';
import AVC from './avc-helper';
import { FlvTag, FlvTagType } from './flv';


// Non-monotonousremux
const DISCONTINUITY_ON_NON_MONOTONOUS = 10;

type TagCache = {
    tag: FlvTag,
    dataOffset: number
}

class FlvDemux {
    private tag = 'FlvDemux';
    private _observer: Observer;
    private _remuxer: IRemuxer;
    private _config: FLVTransConfig;
    private _duration: number;
    private _naluLengthSize: number;
    private _hasVideo: boolean;
    private _hasAudio: boolean;
    private _videoTrack: VTrack;
    private _audioTrack: ATrack;
    private _remuxStat?: { timeOffset: number; contiguous: boolean; accurateTimeOffset: boolean };
    private _currentTimestamp: number = 0;
    private _aLastDTS: number = 0;
    private _vLastDTS: number = 0;
    private _nonMonotonousCache?: Record<string, TagCache[]>;
    private _fillAtStart = {
        enabled: true,
        start: 0,
        avcConfig: true
    };

    constructor(observer: Observer, remuxer: IRemuxer, config: FLVTransConfig) {
        this._observer = observer;
        this._remuxer = remuxer;
        this._config = config;

        this._duration = 0;
        this._naluLengthSize = 4;

        this._hasVideo = true;
        this._hasAudio = true;

        this._videoTrack = {
            id: 1,
            type: TrackType.video,
            container: '',
            codec: '',
            timescale: 90000,
            duration: Infinity,
            samples: [],
            inputTimeScale: 90000,
            sequenceNumber: 0,
            pid: -1,
            width: 0,
            height: 0,
            codecWidth: 0,
            codecHeight: 0,
            sps: [],
            pps: [],
            pixelRatio: [],
            profile: '',
            level: '',
            chromaFormat: '',
            fps: 0,
            dropped: 0,
            refSampleDuration: 0,
        };
        this._audioTrack = {
            id: 2,
            type: TrackType.audio,
            container: '',
            codec: '',
            timescale: 90000,
            duration: Infinity,
            samples: [],
            inputTimeScale: 90000,
            sequenceNumber: 0,
            pid: -1,
            isAAC: true,
            samplerate: 0,
            channelCount: 0,
            config: []
        };
    }

    public append(tags: FlvTag[], timeOffset: number, contiguous: boolean, accurateTimeOffset: boolean): void {
        if (!this._remuxStat) {
            this._remuxStat = { timeOffset, contiguous, accurateTimeOffset };
        }
        if (!tags.length) return;
        tags.forEach(tag => {
            if (tag.tagType === FlvTagType.VIDEO && this._hasVideo) {
                if (this._fillAtStart.enabled) {
                    // tag
                    this._fillAtStart.enabled = false;
                    if (this._audioTrack.samples.length) {
                        this._fillFrame(this._audioTrack.samples[this._audioTrack.samples.length - 1].dts / 90);
                    }
                }
                this._parseVideoData(tag);
            }
            if (tag.tagType === FlvTagType.AUDIO && this._hasAudio) {
                this._parseAudioData(tag);
            }
            if (tag.tagType === FlvTagType.SCRIPT) {
                this._parseScriptTag(tag);
            }
        });

        if (this._fillAtStart.enabled && this._audioTrack.samples.length) {
            // 
            let st = this._audioTrack.samples[0].dts / 90;
            let ed = this._audioTrack.samples[this._audioTrack.samples.length - 1].dts / 90;
            if (this._fillAtStart.start <= 0) {
                this._fillAtStart.start = st;
            }
            if (ed > this._fillAtStart.start) {
                this._fillFrame(ed);
            }
        }
        this._remux();
    }

    public flvHead(hasAudio: boolean, hasVideo: boolean): void {
        this._hasAudio = hasAudio;
        this._hasVideo = hasVideo;
        this._fillAtStart.enabled = this._hasVideo;
    }

    public destroy() { }

    public flush() {
        this._remux(true);
        this._remuxStat = undefined;
    }

    public resetInitSegment() {
        this._videoTrack.samples = [];
        this._audioTrack.samples = [];
        this._aLastDTS = this._vLastDTS = 0;
        this._remuxStat = undefined;
    }

    public resetTimeStamp() { }

    // 
    public set duration(sec: number) {
        this._duration = sec;
    }

    private _parseScriptTag(tag: FlvTag) {
        if (tag.body) {
            const scriptData = AMF.parseMetadata(tag.body.buffer);
            scriptData.timestamp = this._currentTimestamp || 0;
            if (scriptData.hasOwnProperty('onMetaData')) {
                const onMetaData = scriptData.onMetaData;
                if (typeof onMetaData.framerate === 'number') {
                    this._videoTrack.fps = this._videoTrack.fps || onMetaData.framerate;
                }
                Log.i(this.tag, 'Parsed onMetaData');
            }
            this._observer.trigger(LasEvents.SCRIPT_PARSED, scriptData);
        }
    }

    private _parseVideoData(tag: FlvTag) {
        if (!tag.body) {
            return;
        }
        //  video tag body 
        const spec = tag.body[0];
        // 
        tag.frameType = (spec & 240) >>> 4;
        // 
        const codecId = spec & 15;
        if (codecId !== 7 && codecId !== 12) {
            this._observer.trigger(LasEvents.ERROR, {
                type: ErrorTypes.MUX_ERROR,
                details: ErrorDetails.PARSING_ERROR,
                fatal: true,
                reason: `Flv: Unsupported codec in video frame: ${codecId}`
            });
            return;
        }
        tag.codecId = codecId;
        // IF CodecID == 7  AVCPacketType
        // 0 = AVC sequence header
        // 1 = AVC NALU
        // 2 = AVC end of sequence (lower level NALU sequence ender is not required or supported)
        const packetType = tag.body[1];
        // 3
        // IF AVCPacketType == 1
        //  Composition time offset
        // ELSE
        //  0
        tag.cts = ((tag.body[2] & 0xff) << 16) + ((tag.body[3] & 0xff) << 8) + (tag.body[4] & 0xff);

        // IF AVCPacketType == 0 AVCDecoderConfigurationRecordAVC sequence header
        // IF AVCPacketType == 1 One or more NALUs (Full frames are required)
        if (packetType === 0) {
            if (codecId === 7) {
                this._parseAVCDecoderConfigurationRecord(tag, 5);
            }
        } else if (packetType === 1) {
            this._parseAVCVideoData(tag, 5) || {};
        } else if (packetType === 2) {
            // empty, AVC end of sequence
        } else {
            this._observer.trigger(LasEvents.ERROR, {
                type: ErrorTypes.MUX_ERROR,
                details: ErrorDetails.PARSING_ERROR,
                fatal: true,
                reason: `Flv: Invalid video packet type ${packetType}`
            });
            return;
        }
    }

    /**
     * AVC 
     * @param {FlvTag} tag flvtag
     * @param {number} dataOffset tag body offset
     */
    private _parseAVCDecoderConfigurationRecord(tag: FlvTag, dataOffset: number) {
        if (!tag.body) {
            return;
        }
        const track = this._videoTrack;
        const arrayBuffer = tag.body.buffer;
        const dataSize = tag.body.byteLength - dataOffset;
        const v = new DataView(arrayBuffer, dataOffset, dataSize);

        const version = v.getUint8(0); // configurationVersion
        const avcProfile = v.getUint8(1); // avcProfileIndication
        // const profileCompatibility = v.getUint8(2); // profile_compatibility
        // const avcLevel = v.getUint8(3); // AVCLevelIndication
        if (version !== 1 || avcProfile === 0) {
            this._observer.trigger(LasEvents.ERROR, {
                type: ErrorTypes.MUX_ERROR,
                details: ErrorDetails.PARSING_ERROR,
                fatal: true,
                reason: 'Flv: Invalid AVCDecoderConfigurationRecord'
            });
            return;
        }

        this._naluLengthSize = (v.getUint8(4) & 3) + 1; // lengthSizeMinusOne
        if (this._naluLengthSize !== 3 && this._naluLengthSize !== 4) {
            this._observer.trigger(LasEvents.ERROR, {
                type: ErrorTypes.MUX_ERROR,
                details: ErrorDetails.PARSING_ERROR,
                fatal: true,
                reason: `Flv: Strange NaluLengthSizeMinusOne: ${this._naluLengthSize - 1}`
            });
            return;
        }

        const spsCount = v.getUint8(5) & 31; // numOfSequenceParameterSets
        if (spsCount === 0 || spsCount > 1) {
            this._observer.trigger(LasEvents.ERROR, {
                type: ErrorTypes.MUX_ERROR,
                details: ErrorDetails.PARSING_ERROR,
                fatal: true,
                reason: `Flv: Invalid H264 SPS count: ${spsCount}`
            });
            return;
        }

        let offset = 6;
        let spsList = [];
        for (let i = 0; i < spsCount; i++) {
            const len = v.getUint16(offset); // sequenceParameterSetLength
            offset += 2;

            if (len === 0) {
                continue;
            }

            // Notice: Nalu without startcode header (00 00 00 01)
            const sps = new Uint8Array(arrayBuffer, dataOffset + offset, len);
            offset += len;
            spsList.push(sps);
            const config = SPSParser.parseSPS(sps);
            const codecArray = sps.subarray(1, 4);
            let codecString = 'avc1.';
            for (let j = 0; j < 3; j++) {
                let h = codecArray[j].toString(16);
                if (h.length < 2) {
                    h = '0' + h;
                }
                codecString += h;
            }

            // sps update
            if (
                !!track.codec &&
                (track.width !== config.codec_size.width ||
                    track.height !== config.codec_size.height ||
                    codecString !== track.codec)
            ) {
                if (!tag.fill) {
                    this._remux(true);
                }
                if (this._remuxer.ISGenerated) {
                    this._remuxer.resetInitSegment();
                }
            }
            track.sps = spsList;
            track.width = config.codec_size.width;
            track.height = config.codec_size.height;
            track.pixelRatio = [config.sar_ratio.width, config.sar_ratio.height];
            if (config.frame_rate.fixed && config.frame_rate.fps !== 0) {
                track.fps = config.frame_rate.fps;
            }
            track.codec = codecString;

            track.profile = config.profile_string;
            track.level = config.level_string;
            track.chromaFormat = config.chroma_format_string;
        }

        const ppsCount = v.getUint8(offset); // numOfPictureParameterSets
        if (ppsCount === 0 || ppsCount > 1) {
            this._observer.trigger(LasEvents.ERROR, {
                type: ErrorTypes.MUX_ERROR,
                details: ErrorDetails.PARSING_ERROR,
                fatal: true,
                reason: `Flv: Invalid H264 PPS count: ${ppsCount}`
            });
            return;
        }

        offset++;
        track.pps = [];
        for (let i = 0; i < ppsCount; i++) {
            const len = v.getUint16(offset); // pictureParameterSetLength
            offset += 2;
            const pps = new Uint8Array(arrayBuffer, dataOffset + offset, len);
            track.pps.push(pps);
            if (len === 0) {
                continue;
            }
            offset += len;
        }
        Log.v(this.tag, 'Parsed AVCDecoderConfigurationRecord');

        track.duration = this._duration;
        track.container = 'video/mp4';
        if (track.fps) {
            track.refSampleDuration = Math.floor(track.timescale / track.fps);
        } else {
            // fpsfpsmp4sample durationgopremux
            // this._config.gopRemux = true;
            track.refSampleDuration = Math.floor(track.timescale / 30);
        }
    }

    /**
     * AVC 
     * @param {Flvtag} tag flv tag
     * @param {number} dataOffset dataOffset
     */
    private _parseAVCVideoData(tag: FlvTag, dataOffset: number, ignoreNonMonotonous: boolean = false) {
        if (!tag.body) {
            return;
        }
        let info;
        const arrayBuffer = tag.body.buffer;
        const dataSize = tag.body.byteLength - dataOffset;
        const v = new DataView(arrayBuffer, dataOffset, dataSize);

        const units = [];
        let length = 0;

        let offset = 0;
        const lengthSize = this._naluLengthSize;
        const dts = tag.timestamp;
        let keyframe = tag.frameType === 1; // from FLV Frame Type constants
        // Non-monotonous DTS
        if (!ignoreNonMonotonous && dts <= this._vLastDTS && this._vLastDTS > 0) {
            Log.w(this.tag, `debug Non-monotonous DTS dts:${dts} last:${this._vLastDTS}`);
            this._onNonMonotonous({ tag, dataOffset }, TrackType.video);
            return;
        }
        if (!ignoreNonMonotonous && this._nonMonotonousCache) {
            this._flushNonMonotonousCache();
        }
        const pts = dts + tag.cts;
        while (offset < dataSize) {
            if (offset + 4 >= dataSize) {
                Log.v(this.tag, `Malformed Nalu near timestamp ${dts}, offset = ${offset}, dataSize = ${dataSize}`);
                break; // data not enough for next Nalu
            }
            // Nalu with length-header (AVC1)
            let naluSize = v.getUint32(offset); // Big-Endian read
            if (lengthSize === 3) {
                naluSize >>>= 8;
            }
            if (naluSize > dataSize - lengthSize) {
                Log.v(this.tag, `Malformed Nalus near timestamp ${dts}, NaluSize > DataSize!`);
                return;
            }
            const data = new Uint8Array(arrayBuffer, dataOffset + offset + 4, lengthSize + naluSize - 4);
            let unitType;
            if (tag.codecId === 7) {
                // AVC
                unitType = v.getUint8(offset + lengthSize) & 0x1f;
                if (unitType === 5) {
                    keyframe = true;
                }
            }

            const unit = { type: unitType, data };
            units.push(unit);
            length += data.byteLength;
            offset += lengthSize + naluSize;
        }

        if (units.length) {
            const track = this._videoTrack;
            const avcSample = {
                units,
                length,
                dts: dts * 90,
                cts: tag.cts * 90,
                pts: pts * 90,
                streamDTS: dts,
                key: keyframe
            };
            track.samples.push(avcSample);
        }
        this._vLastDTS = dts;
        return info;
    }

    private _parseAudioData(tag: FlvTag, ignoreNonMonotonous: boolean = false) {
        if (!tag.body) {
            return;
        }
        const dataSize = tag.body.byteLength;
        let aacFrameLen;
        if (dataSize <= 1) {
            Log.v(this.tag, 'Flv: Invalid audio packet, missing SoundData payload!');
            return;
        }
        const track = this._audioTrack;

        const packetType = tag.body[1];

        if (packetType === 0) {
            if (tag.body.byteLength < 4) {
                return;
            }
            ASC.initTrackConfig(track, this._observer, tag.body, 0, this._config.audioCodec);
            track.duration = this._duration;

            return;
        } else if (packetType === 1) {
            const aacData = tag.body.subarray(2);
            // AAC raw frame data
            let dts = tag.timestamp;
            // dtsdts
            // aac
            aacFrameLen = ASC.getFrameDuration(track.samplerate);
            let maxAudioFramesDrift = aacFrameLen * this._config.maxAudioFramesDrift;
            if (this._aLastDTS > 0) {
                // 
                dts = this._aLastDTS + aacFrameLen;
                const dtsDiff = tag.timestamp - dts;
                if (!ignoreNonMonotonous && dtsDiff < -maxAudioFramesDrift) {
                    this._onNonMonotonous({ tag }, TrackType.audio);
                    return;
                }
            }
            if (!ignoreNonMonotonous && this._nonMonotonousCache) {
                this._flushNonMonotonousCache();
            }

            const sampleDts = (dts * track.inputTimeScale) / 1000;
            const aacSample = {
                unit: aacData,
                dts: sampleDts,
                pts: sampleDts,
                streamDTS: tag.timestamp,
                length: aacData.byteLength
            };
            this._aLastDTS = dts;
            track.samples.push(aacSample);
        } else {
            Log.v(this.tag, `Flv: Unsupported AAC data type ${packetType}`);
        }
    }

    /**
     * Non-monotonousNon-monotonous
     * @param {object} data tag
     * @param {string} type audio|video
     */
    private _onNonMonotonous(data: any, type: TrackType) {
        if (!this._nonMonotonousCache) {
            this._nonMonotonousCache = {
                video: [],
                audio: []
            };
        }
        const cache = this._nonMonotonousCache[type];
        if (cache.length > DISCONTINUITY_ON_NON_MONOTONOUS) {
            this.flush();
            const lastPts = this._remuxer.getLastPTS();
            let ptsSync: number = lastPts.audio;
            if (ptsSync === 0 || (lastPts.video > 0 && lastPts.video < ptsSync)) {
                ptsSync = lastPts.video;
            }
            this._videoTrack.samples = [];
            this._audioTrack.samples = [];
            this._aLastDTS = this._vLastDTS = 0;
            this._remuxStat = { accurateTimeOffset: false, contiguous: false, timeOffset: ptsSync };
            this._remuxer.resetInitSegment();
            this._remuxer.resetTimeStamp();
            Log.i(this.tag, 'NON_MONOTONOUS reset time');
            this._flushNonMonotonousCache();
        } else {
            cache.push(data);
        }
    }

    /**
     * Non-monotonousremux
     */
    private _flushNonMonotonousCache() {
        if (this._nonMonotonousCache) {
            const nonMonotonousCache = this._nonMonotonousCache;
            for (const key in nonMonotonousCache) {
                const cache = nonMonotonousCache[key];
                while (cache.length) {
                    const data = cache.shift();
                    if (data) {
                        if (key === 'video') {
                            this._parseAVCVideoData(data.tag, data.dataOffset, true);
                        } else if (key === 'audio') {
                            this._parseAudioData(data.tag, true);
                        }
                    }
                }
            }
            this._nonMonotonousCache = undefined;
        }
    }

    private _remux(end = false) {
        const audiotrack = this._audioTrack;
        const videotrack = this._videoTrack;
        let contiguous = true,
            timeOffset = 0,
            accurateTimeOffset = false;
        if (this._remuxStat) {
            contiguous = this._remuxStat.contiguous;
            timeOffset = this._remuxStat.timeOffset;
            accurateTimeOffset = this._remuxStat.accurateTimeOffset;
        }

        if (audiotrack.samples.length === 0 && videotrack.samples.length === 0) {
            if (end) {
                this._remuxer.flush();
            }
            return;
        }

        if (!end && (this._hasAudio && audiotrack.samples.length === 0 || this._hasVideo && videotrack.samples.length < 2)) {
            return;
        }
        try {
            this._remuxer.remux(audiotrack, videotrack, timeOffset, contiguous, accurateTimeOffset, end);
            this._remuxStat = undefined;
        } catch (error) {
            Log.e(this.tag, error);
            this._observer.trigger(LasEvents.ERROR, {
                type: ErrorTypes.MUX_ERROR,
                details: ErrorDetails.REMUX_ERROR,
                fatal: true,
                reason: error.message
            });
        }
    }

    /**
     * 
     * @param ed  ms
     */
    private _fillFrame(ed: number) {
        if (this._audioTrack.samples.length === 0) {
            return;
        }
        let st = this._fillAtStart.start || this._audioTrack.samples[0].dts * 90;
        if (ed > st) {
            Log.i(this.tag, `fill video frame: ${st} ${ed}`);
            this._fillAtStart.start = ed;
            let tags: FlvTag[] = [AVC.getFlvVideoTag(st), AVC.getFlvVideoTag(ed - 1)];
            if (this._fillAtStart.avcConfig) {
                tags.unshift(AVC.getFlvVideoTag(st, true));
                this._fillAtStart.avcConfig = false;
            }
            tags.forEach(tag => {
                this._parseVideoData(tag);
            });
        }
    }
}

export default FlvDemux;
