import { ErrorDetails } from '../core/errors';

const ErrorCodeList: Record<number|string, string> = {
    400: '01', // http 400
    401: '02', // http 401
    403: '03', // http 403
    404: '04', // http 404
    other4xx: '05', // http 4xx
    serverError: '06', // http 5xx
    timeoutOpen: '07',
    timeoutIO: '08',
    200: '09',
    206: '09'
};

export function getErrorCode(detail: ErrorDetails, reason: string, httpStatusCode: number = 0): number {
    if (detail >= 100) {
        return detail;
    }
    let code = '00';
    if (reason === 'timeout') {
        if (!httpStatusCode) {
            code = ErrorCodeList.timeoutOpen || code;;
        } else {
            code = ErrorCodeList.timeoutIO || code;;
        }
    } else if (ErrorCodeList.hasOwnProperty(httpStatusCode)) {
        code = ErrorCodeList[httpStatusCode] || code;
    } else if ((/^4\d{2}$/).test(httpStatusCode.toString())) {
        code = ErrorCodeList.other4xx || code;;
    } else if ((/^5\d{2}$/).test(httpStatusCode.toString())) {
        code = ErrorCodeList.serverError || code;;
    }
    return parseInt(detail + code, 10);
}
