import AbrLevel from "./abr-level";
export type Representation = {
    id: number,  //[int][M]
    url: string, // [string][M]
    backupUrl: string[], // [string][M1]
    host: string, // [string][M2]
    maxBitrate: number, // [int][M] maxBitrate in kbps set by transcoder
    avgBitrate: number, // [int][M1&2] avgBitrate in kbps
    codecs: string, // [string][O]
    width: number,  // [int][M]
    height: number, // [int][M]
    frameRate: number, // [float][O]
    qualityType: string,  // [string][M] 
    qualityLabel: string, // [string][O] 
    hidden: boolean, // [bool][M] false denotes the representation is show in UI and can be selected by manual model
    disableAdaptive: boolean, // [bool][M] false denotes the representation can be selected by auto model
    defaultSelect: boolean // [bool][M1,3,4] false denotes the representation is not selected as the initialization level
}

type AdaptationSet = {
    id: number;
    duration: number;
    representation: Representation[];
};

export type AbrManifestData = {
    version: string,
    adaptationSet: AdaptationSet[];
};

/**
 * las manifest
 */
export class AbrManifest {
    private _levels: AbrLevel[] = [];
    private _abrLevels: number[] = [];
    private _default?: number;

    /**
     * las manifest
     * @param data 
     */
    public static verify(data: any) {
        if (
            data &&
            data.hasOwnProperty('version') &&
            data.hasOwnProperty('adaptationSet') &&
            Array.isArray(data.adaptationSet) &&
            data.adaptationSet.length > 0
        ) {
            return data.adaptationSet.reduce((prev: boolean, item: any) => {
                return !!(prev && item.representation && item.representation.length)
            }, true);
        }
        return false;
    }

    constructor(manifest: AbrManifestData) {
        if (AbrManifest.verify(manifest)) {

            manifest.adaptationSet[0].representation.sort((a, b) => {
                return a.maxBitrate - b.maxBitrate;
            });

            manifest.adaptationSet[0].representation.forEach((item, index) => {
                let level = new AbrLevel(item.url);
                level.id = item.id || 0;
                level.maxBitrate = item.maxBitrate || 0;
                level.avgBitrate = item.avgBitrate || 0;
                level.bitrate = level.maxBitrate;
                level.qualityType = item.qualityType || '';
                level.qualityLabel = item.qualityLabel || '';
                level.codec = item.codecs || '';
                level.hidden = item.hidden || false;
                level.enableAdaptive = typeof item.disableAdaptive === 'undefined' ? true : !item.disableAdaptive;
                level.defaultSelect = item.defaultSelect || false;
                this._levels.push(level);

                if (level.enableAdaptive) {
                    this._abrLevels.push(index);
                }
                if (level.defaultSelect && typeof this._default === 'undefined') {
                    this._default = index;
                }
            });
        } else {
            return;
        }
    }

    /**
     * 
     */
    public get levels(): AbrLevel[] {
        return this._levels;
    }

    /**
     * index
     */
    public get abrLevels(): number[] {
        return this._abrLevels;
    }

    /**
     * 
     */
    public get default(): number {
        return this._default || 0;
    }
}
