/**
 * fMP4 remuxer
 */

import { ErrorDetails, ErrorTypes } from '../core/errors';
import LasEvents from '../core/events';
import Observer from '../core/observer';
import { FLVTransConfig } from '../types/core';
import { ATrack, IRemuxer, TypeSupported, VTrack } from '../types/remux';
import Browser from '../utils/browser';
import { Log } from '../utils/log';
import AAC from './aac-helper';
import MP4 from './mp4-generator';



// 100 seconds
const MAX_SILENT_FRAME_DURATION = 100 * 1000;

type StashInfo = { timeOffset: number; accurateTimeOffset: boolean; track: VTrack; sequenceNumber: number };

type Stat = {
    ISGenerated: boolean;
    nextAudioPts?: number;
    nextAvcDts?: number;
    initPTS?: number;
    initDTS?: number;
    videoTime: {
        nbSamples: number;
        firstDTS: number;
        sampleDuration: number;
        vLastPTS: number;
        aLastPTS: number;
        endDts?: number;
    };
};

class MP4Remuxer implements IRemuxer {
    private _observer: Observer;
    private _config: FLVTransConfig;
    private _typeSupported: TypeSupported;
    private _isSafari: boolean;
    private _forceFirstIDR: boolean;
    private _stash: boolean;
    private _stashInfo: StashInfo;
    private _stashLastVideoSample: any;
    private _ISGenerated: boolean = false;
    private _videoTime!: {
        nbSamples: number;
        firstDTS: number;
        sampleDuration: number;
        vLastPTS: number;
        aLastPTS: number;
        endDts?: number;
    };
    private _extra: any;

    private _nextAudioPts?: number;
    private _nextAvcDts?: number;
    private _initPTS?: number;
    private _initDTS?: number;
    private _initSegment: Record<string, Uint8Array> = {};

    constructor(observer: Observer, config: FLVTransConfig, typeSupported: TypeSupported, vendor: string = '') {
        this._observer = observer;
        this._config = config;
        this._typeSupported = typeSupported;
        const userAgent = navigator.userAgent;
        this._isSafari = !!(vendor && vendor.indexOf('Apple') > -1 && userAgent && !userAgent.match('CriOS'));
        this._ISGenerated = false;
        this._stash = !config.gopRemux;
        // sampleDuration
        this._resetVideoTime();
        this._stashInfo = <StashInfo>{};
        if (!MP4.types) {
            MP4.init();
        }
        // Workaround for chrome < 50: Always force first sample as a Random Access Point in media segment
        // see https://bugs.chromium.org/p/chromium/issues/detail?id=229412
        this._forceFirstIDR =
            Browser.chrome &&
                (Browser.version.major < 50 || (Browser.version.major === 50 && Browser.version.build < 2661))
                ? true
                : false;
    }

    get ISGenerated(): boolean {
        return this._ISGenerated;
    }

    public setStat(stat: Stat): void {
        if (stat) {
            this._ISGenerated = stat.ISGenerated;
            this._nextAudioPts = stat.nextAudioPts;
            this._nextAvcDts = stat.nextAvcDts;
            this._initPTS = stat.initPTS;
            this._initDTS = stat.initDTS;
            this._videoTime.nbSamples = stat.videoTime.nbSamples;
            this._videoTime.firstDTS = stat.videoTime.firstDTS;
            this._videoTime.sampleDuration = stat.videoTime.sampleDuration;
            this._videoTime.vLastPTS = stat.videoTime.vLastPTS;
            this._videoTime.aLastPTS = stat.videoTime.aLastPTS;
            this._videoTime.endDts = stat.videoTime.endDts;
        }
    }
    public getStat(): Stat {
        return {
            ISGenerated: this._ISGenerated,
            nextAudioPts: this._nextAudioPts,
            nextAvcDts: this._nextAvcDts,
            initPTS: this._initPTS,
            initDTS: this._initDTS,
            videoTime: {
                nbSamples: this._videoTime.nbSamples,
                firstDTS: this._videoTime.firstDTS,
                sampleDuration: this._videoTime.sampleDuration,
                vLastPTS: this._videoTime.vLastPTS,
                aLastPTS: this._videoTime.aLastPTS,
                endDts: this._videoTime.endDts
            }
        };
    }

    destroy() { }

    setExtra(data: any) {
        this._extra = data;
    }

    resetTimeStamp(defaultTimeStamp?: number) {
        this._initPTS = this._initDTS = defaultTimeStamp;
        this._resetVideoTime();
    }

    resetInitSegment() {
        this._ISGenerated = false;
        this._initSegment = {};
        this.resetStash();
    }

    getLastPTS() {
        return { video: this._videoTime.vLastPTS, audio: this._videoTime.aLastPTS };
    }

    flush(audioTrackLength = 0) {
        let videoData = null;
        if (this._stash && this._stashLastVideoSample) {
            const info = this._stashInfo;
            info.track.samples = [this._stashLastVideoSample];
            this._stashLastVideoSample = null;
            info.sequenceNumber += 1;
            videoData = this.remuxVideo(info.track, info.timeOffset, true, audioTrackLength, info.accurateTimeOffset);
        }
        this.resetStash();
        return videoData;
    }

    resetStash() {
        this._stashLastVideoSample = null;
        this._stashInfo = <StashInfo>{};
    }

    remux(
        audioTrack: ATrack,
        videoTrack: VTrack,
        timeOffset: number,
        contiguous: boolean,
        accurateTimeOffset: boolean,
        isFlush: boolean = false
    ) {
        // generate Init Segment if needed
        if (!this._ISGenerated) {
            this.generateIS(audioTrack, videoTrack, timeOffset);
        }

        // safari
        if (!contiguous && videoTrack.samples.length) {
            videoTrack.samples[0].pts = videoTrack.samples[0].dts;
        }

        if (this._ISGenerated) {
            const nbVideoStashSamples = isFlush && this._stashLastVideoSample ? 1 : 0;
            const nbAudioSamples = audioTrack.samples.length;
            const nbVideoSamples = videoTrack.samples.length;
            let audioTimeOffset = timeOffset;
            let videoTimeOffset = timeOffset;
            if (nbAudioSamples && nbVideoSamples) {
                if (!contiguous && accurateTimeOffset) {
                    // accurateTimeOffset=truetimeoffset
                    if (audioTrack.samples[0].pts - videoTrack.samples[0].pts < 0) {
                        const sample = Object.assign({}, videoTrack.samples[0]);
                        sample.dts = sample.pts = audioTrack.samples[0].pts;
                        videoTrack.samples.unshift(sample);
                    }
                } else {
                    // timeOffset is expected to be the offset of the first timestamp of this fragment (first DTS)
                    // if first audio DTS is not aligned with first video DTS then we need to take that into account
                    // when providing timeOffset to remuxAudio / remuxVideo.
                    // if we don't do that, there might be a permanent / small drift between audio and video streams
                    const audiovideoDeltaDts =
                        (audioTrack.samples[0].pts - videoTrack.samples[0].pts) / videoTrack.inputTimeScale;
                    audioTimeOffset += Math.max(0, audiovideoDeltaDts);
                    videoTimeOffset += Math.max(0, -audiovideoDeltaDts);
                }
            }
            // Purposefully remuxing audio before video, so that remuxVideo can use nextAudioPts, which is
            // calculated in remuxAudio.
            // Log.v('nb AAC samples:' + audioTrack.samples.length);
            if (nbAudioSamples) {
                // if initSegment was generated without video samples, regenerate it again
                if (!audioTrack.timescale) {
                    Log.w('regenerate InitSegment as audio detected');
                    this.generateIS(audioTrack, videoTrack, timeOffset);
                }
                const audioData = this.remuxAudio(audioTrack, audioTimeOffset, contiguous, accurateTimeOffset);
                // Log.v('nb AVC samples:' + videoTrack.samples.length);
                let audioTrackLength;
                if (audioData) {
                    audioTrackLength = audioData.endPTS - audioData.startPTS;
                }
                if (nbVideoSamples) {
                    // if initSegment was generated without video samples, regenerate it again
                    if (!videoTrack.timescale) {
                        Log.w('regenerate InitSegment as video detected');
                        this.generateIS(audioTrack, videoTrack, timeOffset);
                    }
                    this.remuxVideo(
                        videoTrack,
                        videoTimeOffset,
                        contiguous,
                        audioTrackLength,
                        accurateTimeOffset,
                        isFlush
                    );
                } else {
                    this.flush(audioTrackLength);
                }
            } else {
                // Log.v('nb AVC samples:' + videoTrack.samples.length);
                let videoData = null;
                if (nbVideoSamples) {
                    videoData = this.remuxVideo(
                        videoTrack,
                        videoTimeOffset,
                        contiguous,
                        0,
                        accurateTimeOffset,
                        isFlush
                    );
                } else if (nbVideoStashSamples) {
                    videoData = this.flush();
                }
                if (
                    videoData &&
                    audioTrack.codec &&
                    Number.isFinite(videoData.startDTS) &&
                    Number.isFinite(videoData.endDTS)
                ) {
                    this.remuxEmptyAudio(audioTrack, audioTimeOffset, contiguous, videoData);
                }
            }
        }
    }

    generateIS(audioTrack: ATrack, videoTrack: VTrack, timeOffset: number) {
        const observer = this._observer,
            audioSamples = audioTrack.samples,
            videoSamples = videoTrack.samples,
            typeSupported = this._typeSupported,
            tracks: any = {},
            data = { tracks: tracks },
            computePTSDTS = typeof this._initPTS === 'undefined';
        let container = 'audio/mp4',
            initPTS,
            initDTS;

        if (computePTSDTS) {
            initPTS = initDTS = Infinity;
        }

        if (audioTrack.config && audioSamples.length) {
            // let's use audio sampling rate as MP4 time scale.
            // rationale is that there is a integer nb of audio frames per audio sample (1024 for AAC)
            // using audio sampling rate here helps having an integer MP4 frame duration
            // this avoids potential rounding issue and AV sync issue
            audioTrack.timescale = audioTrack.samplerate;
            Log.v(`audio sampling rate : ${audioTrack.samplerate}`);
            if (!audioTrack.isAAC) {
                if (typeSupported.mpeg) {
                    // Chrome and Safari
                    container = 'audio/mpeg';
                    audioTrack.codec = '';
                } else if (typeSupported.mp3) {
                    // Firefox
                    audioTrack.codec = 'mp3';
                }
            }
            this._initSegment.audio =
                !audioTrack.isAAC && typeSupported.mpeg ? new Uint8Array() : MP4.initSegment([audioTrack]);
            tracks.audio = {
                container: container,
                codec: audioTrack.codec,
                metadata: {
                    channelCount: audioTrack.channelCount,
                    audioSampleRate: audioTrack.samplerate
                }
            };
            if (computePTSDTS) {
                // remember first PTS of this demuxing context. for audio, PTS = DTS
                initPTS = initDTS = audioSamples[0].pts - audioTrack.inputTimeScale * timeOffset;
            }
        }

        if (videoTrack.sps && videoTrack.pps && videoSamples.length) {
            // let's use input time scale as MP4 video timescale
            // we use input time scale straight away to avoid rounding issues on frame duration / cts computation
            const inputTimeScale = videoTrack.inputTimeScale;
            videoTrack.timescale = inputTimeScale;
            this._initSegment.video = MP4.initSegment([videoTrack]);
            tracks.video = {
                container: 'video/mp4',
                codec: videoTrack.codec,
                metadata: {
                    width: videoTrack.width,
                    height: videoTrack.height,
                    fps: videoTrack.fps,
                    profile: videoTrack.profile,
                    level: videoTrack.level,
                    chromaFormat: videoTrack.chromaFormat
                }
            };
            if (computePTSDTS) {
                initPTS = Math.min(initPTS ? initPTS : Infinity, videoSamples[0].pts - inputTimeScale * timeOffset);
                initDTS = Math.min(initDTS ? initDTS : Infinity, videoSamples[0].dts - inputTimeScale * timeOffset);
                this._observer.trigger(LasEvents.INIT_PTS_FOUND, { initPTS: initPTS });
            }
        }

        if (Object.keys(tracks).length) {
            observer.trigger(LasEvents.PARSING_INIT_SEGMENT, data);
            this._ISGenerated = true;
            if (computePTSDTS) {
                this._initPTS = initPTS;
                this._initDTS = initDTS;
            }
        } else {
            observer.trigger(LasEvents.ERROR, {
                type: ErrorTypes.MUX_ERROR,
                details: ErrorDetails.PARSING_ERROR,
                fatal: false,
                reason: 'no audio/video samples found'
            });
        }
    }

    remuxVideo(
        track: VTrack,
        timeOffset: number,
        contiguous: boolean,
        audioTrackLength?: number,
        accurateTimeOffset: boolean = false,
        isFlush: boolean = false
    ) {
        const timeScale = track.timescale,
            inputSamples = track.samples,
            outputSamples = [],
            ptsNormalize = this._PTSNormalize,
            initPTS = this._initPTS,
            streamDTS = inputSamples[0].streamDTS / 1000,
            key = inputSamples[0].key,
            time = this._videoTime;
        let offset = 8,
            mp4SampleDuration = 0,
            mdat,
            moof = null,
            firstPTS = 0,
            firstDTS = 0,
            lastPTS = 0,
            lastDTS = 0,
            nbSamples = inputSamples.length;
        if (typeof initPTS === 'undefined') {
            return;
        }
        // for (let i = 0; i < track.samples.length; i++) {
        //   let avcSample = track.samples[i];
        //   let units = avcSample.units;
        //   let unitsString = '';
        //   for (let j = 0; j < units.length ; j++) {
        //     unitsString += units[j].type + ',';
        //     if (units[j].data.length < 500) {
        //       unitsString += Hex.hexDump(units[j].data);
        //     }
        //   }
        //   Log.v(avcSample.pts + '/' + avcSample.dts + ',' + unitsString + avcSample.units.length);
        // }

        // if parsed fragment is contiguous with last one, let's use last DTS value as reference
        let nextAvcDts = this._nextAvcDts;

        const isSafari = this._isSafari;
        if (nbSamples === 0 || track.timescale === 0) {
            return null;
        }

        // Safari does not like overlapping DTS on consecutive fragments.
        // let's use nextAvcDts to overcome this if fragments are consecutive
        if (isSafari) {
            // also consider consecutive fragments as being contiguous (even if a level switch occurs),
            // for sake of clarity:
            // consecutive fragments are frags with
            //  - less than 100ms gaps between new time offset (if accurate) and next expected PTS OR
            //  - less than 200 ms PTS gaps (timeScale/5)
            contiguous =
                contiguous ||
                !!(
                    inputSamples.length &&
                    nextAvcDts &&
                    ((accurateTimeOffset && Math.abs(timeOffset - nextAvcDts / timeScale) < 0.1) ||
                        Math.abs(inputSamples[0].pts - nextAvcDts - initPTS) < timeScale / 5)
                );
        }

        if (!contiguous) {
            // if not contiguous, let's use target timeOffset
            nextAvcDts = timeOffset * timeScale;
            this._resetVideoTime();
        }
        if (typeof nextAvcDts === 'undefined') {
            return;
        }
        // PTS is coded on 33bits, and can loop from -2^32 to 2^32
        // ptsNormalize will make PTS/DTS value monotonic, we use last known DTS value as reference value
        inputSamples.forEach(function (sample) {
            sample.pts = ptsNormalize(sample.pts - initPTS, nextAvcDts);
            sample.dts = ptsNormalize(sample.dts - initPTS, nextAvcDts);
        });

        // sort video samples by DTS then PTS then demux id order
        inputSamples.sort(function (a, b) {
            const deltadts = a.dts - b.dts;
            const deltapts = a.pts - b.pts;
            return deltadts || (deltapts || a.id - b.id);
        });

        // handle broken streams with PTS < DTS, tolerance up 200ms (18000 in 90kHz timescale)
        const PTSDTSshift = inputSamples.reduce(
            (prev, curr) => Math.max(Math.min(prev, curr.pts - curr.dts), -18000),
            0
        );
        if (PTSDTSshift < 0) {
            Log.w(
                `PTS < DTS detected in video samples, shifting DTS by ${Math.round(
                    PTSDTSshift / 90
                )} ms to overcome this issue`
            );
            for (let i = 0; i < inputSamples.length; i++) {
                inputSamples[i].dts += PTSDTSshift;
            }
        }
        // sampleremuxsampleDuration
        if (this._stash) {
            this._stashInfo.timeOffset = timeOffset;
            this._stashInfo.accurateTimeOffset = accurateTimeOffset;
            this._stashInfo.track = track;
            if (this._stashLastVideoSample) {
                nbSamples++;
                inputSamples.unshift(this._stashLastVideoSample);
                this._stashLastVideoSample = null;
            }
            if (inputSamples.length > 1 && !isFlush) {
                this._stashLastVideoSample = inputSamples.pop();
                nbSamples--;
            }
        }
        // compute first DTS and last DTS, normalize them against reference value
        let sample = inputSamples[0];
        firstDTS = Math.max(sample.dts, 0);
        firstPTS = Math.max(sample.pts, 0);

        // check timestamp continuity accross consecutive fragments (this is to remove inter-fragment gap/hole)
        const delta = Math.round((firstDTS - nextAvcDts) / 90);
        // if fragment are contiguous, detect hole/overlapping between fragments
        if (contiguous) {
            if (delta) {
                if (delta > 1) {
                    Log.v(`AVC:${delta} ms hole between fragments detected,filling it`);
                } else if (delta < -1) {
                    Log.v(`AVC:${-delta} ms overlapping between fragments detected`);
                }

                firstPTS = Math.max(firstPTS - (firstDTS - nextAvcDts), nextAvcDts);
                // remove hole/gap : set DTS to next expected DTS
                firstDTS = nextAvcDts;
                inputSamples[0].dts = firstDTS;
                // offset PTS as well, ensure that PTS is smaller or equal than new DTS
                inputSamples[0].pts = firstPTS;
                Log.v(
                    `Video/PTS/DTS adjusted: ${Math.round(firstPTS / 90)}/${Math.round(
                        firstDTS / 90
                    )},delta:${delta} ms`
                );
            }
        }

        // compute lastPTS/lastDTS
        sample = inputSamples[inputSamples.length - 1];
        lastDTS = Math.max(sample.dts, 0);
        lastPTS = Math.max(sample.pts, 0, lastDTS);

        // on Safari let's signal the same sample duration for all samples
        // sample duration (as expected by trun MP4 boxes), should be the delta between sample DTS
        // set this constant duration as being the avg delta between consecutive DTS.
        if (isSafari) {
            mp4SampleDuration = Math.round((lastDTS - firstDTS) / (inputSamples.length - 1));
        }

        let nbNalu = 0,
            naluLen = 0;
        for (let i = 0; i < nbSamples; i++) {
            // compute total/avc sample length and nb of NAL units
            const sample = inputSamples[i],
                units = sample.units,
                nbUnits = units.length;
            let sampleLen = 0;
            for (let j = 0; j < nbUnits; j++) {
                sampleLen += units[j].data.length;
            }

            naluLen += sampleLen;
            nbNalu += nbUnits;
            sample.length = sampleLen;

            // normalize PTS/DTS
            if (isSafari) {
                // sample DTS is computed using a constant decoding offset (mp4SampleDuration) between samples
                sample.dts = firstDTS + i * mp4SampleDuration;
            } else {
                // ensure sample monotonic DTS
                sample.dts = Math.max(sample.dts, firstDTS);
            }
            // ensure that computed value is greater or equal than sample DTS
            sample.pts = Math.max(sample.pts, sample.dts);
        }

        /* concatenate the video data and construct the mdat in place
      (need 8 more bytes to fill length and mpdat type) */
        const mdatSize = naluLen + 4 * nbNalu + 8;
        try {
            mdat = new Uint8Array(mdatSize);
        } catch (err) {
            this._observer.trigger(LasEvents.ERROR, {
                type: ErrorTypes.MUX_ERROR,
                details: ErrorDetails.REMUX_ALLOC_ERROR,
                fatal: false,
                bytes: mdatSize,
                reason: `fail allocating video mdat ${mdatSize}`
            });
            return null;
        }
        const view = new DataView(mdat.buffer);
        view.setUint32(0, mdatSize);
        mdat.set(MP4.types.mdat, 4);

        for (let i = 0; i < nbSamples; i++) {
            const avcSample = inputSamples[i],
                avcSampleUnits = avcSample.units;
            let mp4SampleLength = 0,
                compositionTimeOffset;
            // convert NALU bitstream to MP4 format (prepend NALU with size field)
            for (let j = 0, nbUnits = avcSampleUnits.length; j < nbUnits; j++) {
                const unit = avcSampleUnits[j],
                    unitData = unit.data,
                    unitDataLen = unit.data.byteLength;
                view.setUint32(offset, unitDataLen);
                offset += 4;
                mdat.set(unitData, offset);
                offset += unitDataLen;
                mp4SampleLength += 4 + unitDataLen;
            }

            if (!isSafari) {
                // expected sample duration is the Decoding Timestamp diff of consecutive samples
                if (i < nbSamples - 1) {
                    mp4SampleDuration = inputSamples[i + 1].dts - avcSample.dts;
                } else {
                    const config = this._config;
                    let lastFrameDuration = 0;
                    if (this._stashLastVideoSample) {
                        lastFrameDuration = this._stashLastVideoSample.dts - avcSample.dts;
                    } else {
                        lastFrameDuration =
                            track.refSampleDuration ||
                            time.sampleDuration ||
                            avcSample.dts - inputSamples[i > 0 ? i - 1 : i].dts;
                    }
                    lastFrameDuration = Math.floor(lastFrameDuration / 90) * 90;
                    if (config.stretchShortVideoTrack && this._nextAudioPts) {
                        // In some cases, a segment's audio track duration may exceed the video track duration.
                        // Since we've already remuxed audio, and we know how long the audio track is, we look to
                        // see if the delta to the next segment is longer than maxBufferHole.
                        // If so, playback would potentially get stuck, so we artificially inflate
                        // the duration of the last frame to minimize any potential gap between segments.

                        const maxBufferHole = config.maxBufferHole,
                            gapTolerance = Math.floor(maxBufferHole * timeScale),
                            deltaToFrameEnd =
                                (audioTrackLength ? firstPTS + audioTrackLength * timeScale : this._nextAudioPts) -
                                avcSample.pts;
                        if (deltaToFrameEnd > gapTolerance) {
                            // We subtract lastFrameDuration from deltaToFrameEnd to try to prevent any video
                            // frame overlap. maxBufferHole should be >> lastFrameDuration anyway.
                            mp4SampleDuration = deltaToFrameEnd - lastFrameDuration;
                            if (mp4SampleDuration < 0) {
                                mp4SampleDuration = lastFrameDuration;
                            }

                            Log.v(
                                `It is approximately ${deltaToFrameEnd /
                                90} ms to the next segment; using duration ${mp4SampleDuration /
                                90} ms for the last video frame.`
                            );
                        } else {
                            mp4SampleDuration = lastFrameDuration;
                        }
                    } else {
                        mp4SampleDuration = lastFrameDuration;
                    }
                }
                compositionTimeOffset = Math.round(avcSample.pts - avcSample.dts);
            } else {
                compositionTimeOffset = Math.max(
                    0,
                    mp4SampleDuration * Math.round((avcSample.pts - avcSample.dts) / mp4SampleDuration)
                );
            }

            // samplemp4SampleDurationfpsdurationduration
            if (mp4SampleDuration < 0 || isNaN(mp4SampleDuration)) {
                let fix = 0;
                if (contiguous && inputSamples.length < 2) {
                    fix = delta * 90;
                }
                mp4SampleDuration = Math.max((track.refSampleDuration || time.sampleDuration) - fix, 90);
            }
            // console.log('mp4SampleDuration', mp4SampleDuration);
            outputSamples.push({
                size: mp4SampleLength,
                // constant duration
                duration: mp4SampleDuration,
                cts: compositionTimeOffset,
                flags: {
                    isLeading: 0,
                    isDependedOn: 0,
                    hasRedundancy: 0,
                    degradPrio: 0,
                    dependsOn: avcSample.key ? 2 : 1,
                    isNonSync: avcSample.key ? 0 : 1
                }
            });
        }
        // next AVC sample DTS should be equal to last sample DTS + last sample duration (in PES timescale)

        this._nextAvcDts = lastDTS + mp4SampleDuration;
        const dropped = track.dropped;
        track.dropped = 0;
        if (outputSamples.length && this._forceFirstIDR) {
            const flags = outputSamples[0].flags;
            // chrome workaround, mark first sample as being a Random Access Point to avoid sourcebuffer append issue
            // https://code.google.com/p/chromium/issues/detail?id=229412
            flags.dependsOn = 2;
            flags.isNonSync = 0;
        }
        track.samples = outputSamples;
        moof = MP4.moof(track.sequenceNumber++, firstDTS, track);
        track.samples = [];

        const data: any = {
            payload: this._mergeBoxes('video', moof, mdat),
            startPTS: firstPTS / timeScale,
            endPTS: (lastPTS + mp4SampleDuration) / timeScale,
            startDTS: firstDTS / timeScale,
            endDTS: this._nextAvcDts / timeScale,
            type: 'video',
            hasAudio: false,
            hasVideo: true,
            nb: outputSamples.length,
            dropped: dropped,
            streamDTS,
            key,
            extra: this._extra,
        };
        this._videoTime.vLastPTS = data.endPTS;
        this._observer.trigger(LasEvents.PARSING_DATA, data);

        // duration
        if (time.nbSamples === 0) {
            time.firstDTS = firstDTS;
        }
        time.nbSamples += nbSamples;
        time.sampleDuration = Math.round((this._nextAvcDts - time.firstDTS) / time.nbSamples);
        time.endDts = this._nextAvcDts;

        return data;
    }

    remuxAudio(track: ATrack, timeOffset: number, contiguous: boolean, accurateTimeOffset: boolean = false) {
        if (!track.samples.length) {
            return null;
        }
        const inputTimeScale = track.inputTimeScale,
            mp4timeScale = track.timescale,
            scaleFactor = inputTimeScale / mp4timeScale,
            mp4SampleDuration = track.isAAC ? 1024 : 1152,
            inputSampleDuration = mp4SampleDuration * scaleFactor,
            ptsNormalize = this._PTSNormalize,
            initPTS = this._initPTS,
            rawMPEG = !track.isAAC && this._typeSupported.mpeg,
            outputSamples = [],
            streamDTS = track.samples[0].streamDTS / 1000;
        let offset = rawMPEG ? 0 : 8,
            mp4Sample,
            fillFrame,
            mdat,
            moof,
            firstPTS,
            lastPTS,
            inputSamples = track.samples,
            nextAudioPts = this._nextAudioPts,
            resetPts = false;
        if (typeof initPTS === 'undefined') {
            return;
        }
        // for audio samples, also consider consecutive fragments as being contiguous (even if a level switch occurs),
        // for sake of clarity:
        // consecutive fragments are frags with
        //  - less than 100ms gaps between new time offset (if accurate) and next expected PTS OR
        //  - less than 20 audio frames distance
        // contiguous fragments are consecutive fragments from same quality level (same level, new SN = old SN + 1)
        // this helps ensuring audio continuity and this also avoids audio glitches/cut when switching quality,
        // or reporting wrong duration on first audio frame
        contiguous =
            contiguous ||
            !!(
                inputSamples.length &&
                nextAudioPts &&
                ((accurateTimeOffset && Math.abs(timeOffset - nextAudioPts / inputTimeScale) < 0.1) ||
                    Math.abs(inputSamples[0].pts - nextAudioPts - initPTS) < 20 * inputSampleDuration)
            );

        // compute normalized PTS
        inputSamples.forEach(function (sample) {
            sample.pts = sample.dts = ptsNormalize(sample.pts - initPTS, timeOffset * inputTimeScale);
        });

        // filter out sample with negative PTS that are not playable anyway
        // if we don't remove these negative samples, they will shift all audio samples forward.
        // leading to audio overlap between current / next fragment
        inputSamples = inputSamples.filter(function (sample) {
            return sample.pts >= 0;
        });

        // in case all samples have negative PTS, and have been filtered out, return now
        if (inputSamples.length === 0) {
            return null;
        }
        if (!contiguous || typeof nextAudioPts === 'undefined') {
            if (!accurateTimeOffset) {
                // if frag are mot contiguous and if we cant trust time offset,
                // let's use first sample PTS as next audio PTS
                nextAudioPts = inputSamples[0].pts;
            } else {
                // if timeOffset is accurate, let's use it as predicted next audio PTS
                nextAudioPts = timeOffset * inputTimeScale;
            }
            resetPts = true;
        }
        // If the audio track is missing samples, the frames seem to get "left-shifted" within the
        // resulting mp4 segment, causing sync issues and leaving gaps at the end of the audio segment.
        // In an effort to prevent this from happening, we inject frames here where there are gaps.
        // When possible, we inject a silent frame; when that's not possible, we duplicate the last
        // frame.
        if (typeof nextAudioPts === 'undefined') {
            return;
        }
        if (track.isAAC) {
            const maxAudioFramesDrift = this._config.maxAudioFramesDrift;
            for (let i = 0, nextPts = nextAudioPts; i < inputSamples.length;) {
                // First, let's see how far off this frame is from where we expect it to be
                const sample = inputSamples[i],
                    pts = sample.pts,
                    delta = pts - nextPts;

                const duration = Math.abs((1000 * delta) / inputTimeScale);

                // If we're overlapping by more than a duration, drop this sample
                if (delta <= -maxAudioFramesDrift * inputSampleDuration) {
                    Log.w(
                        `Dropping 1 audio frame @ ${(nextPts / inputTimeScale).toFixed(3)}s due to ${Math.round(
                            duration
                        )} ms overlap.`
                    );
                    inputSamples.splice(i, 1);
                    // Don't touch nextPtsNorm or i
                } // eslint-disable-line brace-style

                // Insert missing frames if:
                // 1: We're more than maxAudioFramesDrift frame away
                // 2: Not more than MAX_SILENT_FRAME_DURATION away
                // 3: currentTime (aka nextPtsNorm) is not 0
                else if (
                    delta >= maxAudioFramesDrift * inputSampleDuration &&
                    duration < MAX_SILENT_FRAME_DURATION &&
                    nextPts
                ) {
                    const missing = Math.round(delta / inputSampleDuration);
                    Log.w(
                        `Injecting ${missing} audio frame @ ${(nextPts / inputTimeScale).toFixed(
                            3
                        )}s due to ${Math.round((1000 * delta) / inputTimeScale)} ms gap.`
                    );
                    for (let j = 0; j < missing; j++) {
                        const newStamp = Math.max(nextPts, 0);
                        fillFrame = AAC.getSilentFrame(track.manifestCodec || track.codec, track.channelCount);
                        if (!fillFrame) {
                            Log.v('Unable to get silent frame for given audio codec; duplicating last frame instead.');
                            fillFrame = sample.unit.subarray();
                        }
                        inputSamples.splice(i, 0, { unit: fillFrame, pts: newStamp, dts: newStamp });
                        nextPts += inputSampleDuration;
                        i++;
                    }

                    // Adjust sample to next expected pts
                    sample.pts = sample.dts = nextPts;
                    nextPts += inputSampleDuration;
                    i++;
                } else {
                    // Otherwise, just adjust pts
                    if (Math.abs(delta) > 0.1 * inputSampleDuration) {
                        // Log.v(
                        //     `Invalid frame delta ${Math.round(delta + inputSampleDuration)} at PTS ${Math.round(
                        //         pts / 90
                        //     )} (should be ${Math.round(inputSampleDuration)}).`
                        // );
                    }
                    sample.pts = sample.dts = nextPts;
                    nextPts += inputSampleDuration;
                    i++;
                }
            }
        }

        // compute mdat size, as we eventually filtered/added some samples
        let nbSamples = inputSamples.length;
        let mdatSize = 0;
        while (nbSamples--) {
            mdatSize += inputSamples[nbSamples].unit.byteLength;
        }

        for (let j = 0, nbSamples = inputSamples.length; j < nbSamples; j++) {
            const audioSample = inputSamples[j];
            const unit = audioSample.unit;
            let pts = audioSample.pts;
            // console.log('audioSample.pts', audioSample);
            // Log.v(`Audio/PTS:${Math.round(pts/90)}`);
            // if not first sample
            if (typeof lastPTS !== 'undefined' && mp4Sample) {
                mp4Sample.duration = Math.round((pts - lastPTS) / scaleFactor);
            } else {
                const delta = Math.round((1000 * (pts - nextAudioPts)) / inputTimeScale);
                let numMissingFrames = 0;
                // if fragment are contiguous, detect hole/overlapping between fragments
                // contiguous fragments are consecutive fragments from same quality level
                // (same level, new SN = old SN + 1)
                if (contiguous && track.isAAC) {
                    // log delta
                    if (delta) {
                        if (delta > 0 && delta < MAX_SILENT_FRAME_DURATION) {
                            numMissingFrames = Math.round((pts - nextAudioPts) / inputSampleDuration);
                            Log.v(`${delta} ms hole between AAC samples detected,filling it`);
                            if (numMissingFrames > 0) {
                                fillFrame = AAC.getSilentFrame(track.manifestCodec || track.codec, track.channelCount);
                                if (!fillFrame) {
                                    fillFrame = unit.subarray();
                                }

                                mdatSize += numMissingFrames * fillFrame.length;
                            }
                            // if we have frame overlap, overlapping for more than half a frame duraion
                        } else if (delta < -12) {
                            // drop overlapping audio frames... browser will deal with it
                            Log.v(
                                `drop overlapping AAC sample, expected/parsed/delta:${(
                                    nextAudioPts / inputTimeScale
                                ).toFixed(3)}s/${(pts / inputTimeScale).toFixed(3)}s/${-delta}ms`
                            );
                            mdatSize -= unit.byteLength;
                            continue;
                        }
                        // set PTS/DTS to expected PTS/DTS
                        pts = nextAudioPts;
                    }
                }
                // remember first PTS of our audioSamples
                firstPTS = pts;
                if (mdatSize > 0) {
                    mdatSize += offset;
                    try {
                        mdat = new Uint8Array(mdatSize);
                    } catch (err) {
                        this._observer.trigger(LasEvents.ERROR, {
                            type: ErrorTypes.MUX_ERROR,
                            details: ErrorDetails.REMUX_ALLOC_ERROR,
                            fatal: false,
                            bytes: mdatSize,
                            reason: `fail allocating audio mdat ${mdatSize}`
                        });
                        return null;
                    }
                    if (!rawMPEG) {
                        const view = new DataView(mdat.buffer);
                        view.setUint32(0, mdatSize);
                        mdat.set(MP4.types.mdat, 4);
                    }
                } else {
                    track.samples = [];
                    // no audio samples
                    return null;
                }
                for (let i = 0; i < numMissingFrames; i++) {
                    fillFrame = AAC.getSilentFrame(track.manifestCodec || track.codec, track.channelCount);
                    if (!fillFrame) {
                        Log.v('Unable to get silent frame for given audio codec; duplicating this frame instead.');
                        fillFrame = unit.subarray();
                    }
                    mdat.set(fillFrame, offset);
                    offset += fillFrame.byteLength;
                    mp4Sample = {
                        size: fillFrame.byteLength,
                        cts: 0,
                        duration: 1024,
                        flags: {
                            isLeading: 0,
                            isDependedOn: 0,
                            hasRedundancy: 0,
                            degradPrio: 0,
                            dependsOn: 1
                        }
                    };
                    outputSamples.push(mp4Sample);
                }
            }
            if (!mdat) {
                return;
            }
            mdat.set(unit, offset);
            const unitLen = unit.byteLength;
            offset += unitLen;
            // PTS/DTS/initDTS/normPTS/normDTS/relative PTS
            mp4Sample = {
                size: unitLen,
                cts: 0,
                duration: 0,
                flags: {
                    isLeading: 0,
                    isDependedOn: 0,
                    hasRedundancy: 0,
                    degradPrio: 0,
                    dependsOn: 1
                }
            };
            outputSamples.push(mp4Sample);
            lastPTS = pts;
        }
        let lastSampleDuration = 0;
        nbSamples = outputSamples.length;
        if (!mp4Sample) {
            return;
        }
        // set last sample duration as being identical to previous sample
        if (nbSamples >= 2) {
            lastSampleDuration = outputSamples[nbSamples - 2].duration;
            mp4Sample.duration = lastSampleDuration;
        }
        if (nbSamples && typeof lastPTS === 'number' && mdat) {
            if (mp4Sample && mp4Sample.duration === 0) {
                mp4Sample.duration = track.isAAC ? 1024 : 1152;
            }
            // next audio sample PTS should be equal to last sample PTS + duration
            this._nextAudioPts = nextAudioPts = lastPTS + scaleFactor * (lastSampleDuration || mp4SampleDuration);
            // Log.v('Audio/PTS/PTSend:' + audioSample.pts.toFixed(0) + '/' + this.nextAacDts.toFixed(0));
            track.samples = outputSamples;
            if (rawMPEG) {
                moof = new Uint8Array();
            } else {
                moof = MP4.moof(track.sequenceNumber++, firstPTS / scaleFactor, track);
            }

            track.samples = [];
            const start = firstPTS / inputTimeScale;
            const end = nextAudioPts / inputTimeScale;
            const audioData = {
                payload: this._mergeBoxes('audio', moof, mdat),
                startPTS: start,
                endPTS: end,
                startDTS: start,
                endDTS: end,
                type: 'audio',
                hasAudio: true,
                hasVideo: false,
                nb: nbSamples,
                streamDTS,
                extra: this._extra,
            };
            this._videoTime.aLastPTS = audioData.endPTS;
            this._observer.trigger(LasEvents.PARSING_DATA, audioData);
            return audioData;
        }
        if (resetPts) {
            delete this._nextAudioPts;
        }
        track.samples = [];
        return null;
    }

    remuxEmptyAudio(track: ATrack, timeOffset: number, contiguous: boolean, videoData: any) {
        if (typeof this._initDTS === 'undefined') {
            return;
        }
        const inputTimeScale = track.inputTimeScale,
            mp4timeScale = track.samplerate ? track.samplerate : inputTimeScale,
            scaleFactor = inputTimeScale / mp4timeScale,
            nextAudioPts = this._nextAudioPts,
            // sync with video's timestamp
            startDTS =
                (typeof nextAudioPts !== 'undefined' ? nextAudioPts : videoData.startDTS * inputTimeScale) +
                this._initDTS,
            endDTS = videoData.endDTS * inputTimeScale + this._initDTS,
            // one sample's duration value
            sampleDuration = 1024,
            frameDuration = scaleFactor * sampleDuration,
            // samples count of this segment's duration
            nbSamples = Math.ceil((endDTS - startDTS) / frameDuration),
            // silent frame
            silentFrame = AAC.getSilentFrame(track.manifestCodec || track.codec, track.channelCount);

        Log.w('remux empty Audio');
        // Can't remux if we can't generate a silent frame...
        if (!silentFrame) {
            Log.d('Unable to remuxEmptyAudio since we were unable to get a silent frame for given audio codec!');
            return;
        }

        const samples = [];
        for (let i = 0; i < nbSamples; i++) {
            const stamp = startDTS + i * frameDuration;
            samples.push({ unit: silentFrame, pts: stamp, dts: stamp });
        }
        track.samples = samples;

        this.remuxAudio(track, timeOffset, contiguous);
    }

    _PTSNormalize(value: number, reference?: number) {
        let offset;
        if (typeof reference === 'undefined') {
            return value;
        }

        if (reference < value) {
            // - 2^33
            offset = -8589934592;
        } else {
            // + 2^33
            offset = 8589934592;
        }
        /* PTS is 33bit (from 0 to 2^33 -1)
      if diff between value and reference is bigger than half of the amplitude (2^32) then it means that
      PTS looping occured. fill the gap */
        while (Math.abs(value - reference) > 4294967296) {
            value += offset;
        }

        return value;
    }

    _resetVideoTime() {
        this._videoTime = { nbSamples: 0, firstDTS: 0, sampleDuration: 3000, vLastPTS: 0, aLastPTS: 0 };
    }

    private _mergeBoxes(type: string, ...arr: Uint8Array[]): Uint8Array {
        if (this._initSegment[type]) {
            arr.unshift(this._initSegment[type]);
            delete this._initSegment[type];
        }
        const len = arr.reduce((prev, cur) => {
            if (cur) {
                return prev + cur.byteLength;
            }
            return prev;
        }, 0);
        const result = new Uint8Array(len);
        let index = 0;
        arr.forEach(cur => {
            if (cur) {
                result.set(cur, index);
                index += cur.byteLength;
            }
        });
        return result;
    }
}

export default MP4Remuxer;
